import './scss/styles.scss';
import React, { useEffect } from "react";
import ReactDOM from "react-dom";
//import "./modules/preloadImages";
import { Switch, Route, HashRouter } from 'react-router-dom';
import ScrollToTop from './components/ScrollToTop';


import { gsap } from 'gsap/gsap-core';
import { ScrollTrigger } from 'gsap/ScrollTrigger';


//Componentes
import { Provider } from './Store';
import Header from './components/Header';
import Introduccion from './sections/Introduccion/index';
import EstadoActual from './sections/EstadoActual';
import EvaluacionRiesgo from './sections/EvaluacionRiesgo';
import Conclusiones from './sections/Conclusiones/index.js';
import PerdidaEcosistemas from "./sections/PerdidaEcosistemas/";
import ProcesosEcologicos from "./sections/ProcesosEcologicos/"
import ConservacionAreasProtegidas from "./sections/ConservacionAreasProtegidas/"
import Creditos from "./sections/Creditos/";
import NoMatchPage from './components/NoMatchPage'


gsap.registerPlugin(ScrollTrigger);
gsap.core.globals('ScrollTrigger', ScrollTrigger);
const App = () => {

  useEffect(()=>{
    var e = document.createElement("base");
    e.target = "_blank";
    document.head.appendChild(e);
  }, []);
  return (
    <HashRouter>
      <Provider>
        <ScrollToTop />
        <Header />
        <Switch>
          <Route exact path='/' component={Introduccion} />
          <Route exact path='/introduccion' component={Introduccion} />
          <Route exact path='/introduction' component={Introduccion} />

          <Route exact path="/evaluacion-del-riesgo" component={EvaluacionRiesgo}/>
          <Route exact path="/risk-assessment" component={EvaluacionRiesgo}/>

          <Route exact path='/perdida-de-ecosistemas' component={PerdidaEcosistemas} />
          <Route exact path='/loss-of-ecosystems' component={PerdidaEcosistemas} />

          <Route exact path='/estado-actual-de-ecosistemas-en-riesgo' component={EstadoActual} />
          <Route exact path='/current-condition-of-ecosystems-under-high-risk' component={EstadoActual} />

          <Route exact path='/procesos-ecologicos' component={ProcesosEcologicos} />
          <Route exact path='/ecological-processes' component={ProcesosEcologicos} />

          <Route exact path='/aplicaciones-en-conservacion-y-restauracion' component={ConservacionAreasProtegidas} />
          <Route exact path='/applications-in-conservation-and-restoration-' component={ConservacionAreasProtegidas} />

          <Route exact path='/conclusiones' component={Conclusiones} />
          <Route exact path='/conclusions' component={Conclusiones} />

          <Route exact path='/creditos' component={Creditos} />
          <Route exact path='/credits' component={Creditos} />

          <Route component={NoMatchPage} />
        </Switch>

      </Provider>
    </HashRouter>
  )
}
ReactDOM.render(<App />, document.getElementById("root"));

