import { useState, useCallback, useEffect } from 'react';

function useHeightContainer() {
  const [heightContainer, setHeightContainer] = useState(0);
  const [headerSpace, setHeaderSpace] = useState(56);
  const [windowHeight, setWindowHeight] = useState(window.innerHeight);

  const containerRef = useCallback(node=>{
    const createHeight = () => {
      if(node!== null){
        let header = document.getElementsByTagName('header')[0];
        let mainMenu = document.getElementsByClassName('main-menu')[0];
        let height = windowHeight -(header.getBoundingClientRect().height + mainMenu.getBoundingClientRect().height);
        setHeightContainer(height);
        setHeaderSpace(header.getBoundingClientRect().height-5)
      }
    }
    createHeight();
  }, [])

  useEffect(()=>{
    const changeHeight = () => {
      setWindowHeight(window.innerHeight)
    };
    window.addEventListener('resize', changeHeight)
    return()=>{
      window.removeEventListener('resize', changeHeight)
    }
  }, [])

  return [heightContainer, containerRef, headerSpace]
}

export { useHeightContainer};