import React from "react";
import { useTrackedState } from '../../../Store';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';

const PortadaBosquesHumedosTropicales = () => {
    const state = useTrackedState();
    const {language} = state;
    const [heightContainer, containerRef] = useGeneralHeightContainer();
    return(
      <div 
        ref={containerRef}
        style={{height: `${heightContainer}px`}}
        className="height-responsive"
      >
        <div className="container d-flex justify-content-center h-100  height-responsive pt-5 pt-lg-0 pl-5 pl-lg-0">
          <div className="text-center">
            <img 
              src='img/estado_actual/bosques_subhumedos_tropicales/portada2.jpg' 
              alt="imagen de Bosques subhúmedos tropicales altoandinos" 
              className="img-portada"
            />
            {language === 'es' &&
              <h3 >Bosques subhúmedos<br/>tropicales altoandinos
              </h3>
            }
            {language === 'en' &&
              <h3>Sub-Humid Tropical Forests <br/>of the Andean Highlands
              </h3>
            }
          </div>
        </div>
      </div>
    )
  }

  export default PortadaBosquesHumedosTropicales;