import React from 'react';
import { Element } from 'react-scroll';

import { gsap } from 'gsap/gsap-core';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

//Componentes
import FadeElement from '../../components/FadeElement';
import MenuLateral from '../../components/MenuLateral';
import AplicacionesConservacionRestauracion from './ConservacionAreasProtegidas';
import RestauracionEcosistemas from './RestauracionEcosistemas';
import EspacioInferior from '../../components/EspacioInferior';
import { useTrackedState } from '../../Store';
import normalizeString from '../../js/utilities/normalizeString';

gsap.registerPlugin(ScrollTrigger);
gsap.core.globals('ScrollTrigger', ScrollTrigger);

const AplicacionesConservacionRestauracion_App = () => {

  const state = useTrackedState();
  const { language } = state;
  
  const secciones = {
    es: ['Conservación y áreas protegidas nacionales', 'Restauración de ecosistemas'],
    en: ['Conservation and National Protected Areas', 'Ecosystem restoration']
  }

  return (
    <>
      <MenuLateral
        section={'aplicaciones-en-conservacion-y-restauracion'}
        subsections={secciones}
      />
      <Element name={`aplicaciones-en-conservacion-y-restauracion-${normalizeString(secciones[language][0])}`}>
        <AplicacionesConservacionRestauracion />
      </Element>
      <Element name={`aplicaciones-en-conservacion-y-restauracion-${normalizeString(secciones[language][1])}`}>
        <RestauracionEcosistemas />
      </Element>
      <FadeElement />
      <EspacioInferior />
    </>
  )
}

export default AplicacionesConservacionRestauracion_App;