import React, { useEffect, useRef } from 'react';
import ReactTooltip from 'react-tooltip';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import calcularPropiedadesScrollTrigger from '../../js/utilities/calcularPropiedadesScrollTrigger';
import EspacioInferior from '../../components/EspacioInferior';

const ProcesosEcologicos = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        useEffect(() => {
            ReactTooltip.rebuild();
        }, [language]);
        return (
            <>
                {language === 'es' &&
                    <>
                        <h2 className="mb-5">Procesos ecológicos</h2>
                        <h3 className="subtitulo pl-3">Algunas interacciones entre animales y plantas</h3>
                        <p className="mt-5">Entre los muchos procesos ecológicos (véase el criterio D), los procesos mutualistas de interacción planta-animal cumplen un papel fundamental en el mantenimiento de la integridad ecosistémica al condicionar otros procesos, como el establecimiento de semillas, el reclutamiento de plantas y la regeneración <span className="nota-al-pies-number" data-tip="<span>[15]</span>. Fontúrbel, F. E., Candia, A. B., Malebrán, J., Salazar, D. A., González-Browne, C. y Medel, R. (2015). Meta-analysis of anthropogenic habitat disturbance effects on animal-mediated seed dispersal. <em>Global Change Biology</em>, 21(11), 3951–3960. Recuperado de <a href='https://doi.org/10.1111/gcb.13025'>https://doi.org/10.1111/gcb.13025</a>" data-for="tooltip-procesos-ecologicos">[15]</span>. En el trópico, en particular en los ecosistemas de bosques húmedos tropicales, estos procesos son especialmente dominantes e importantes para el funcionamiento de los ecosistemas y sus servicios. En ellos el 85 % de los árboles dependen de animales para cumplir sus ciclos reproductivos, con frecuencia de especies particulares <span className="nota-al-pies-number" data-tip="<span>[16]</span>. Neuschulz, E. L., Mueller, T., Schleuning, M. y Böhning-Gaese, K. (2016). Pollination and seed dispersal are the most threatened processes of plant regeneration. <em>Scientific Reports</em>, 6(29839), 1–6. doi: 10.1038/srep29839 " data-for="tooltip-procesos-ecologicos">[16]</span>.</p>

                        <p>Para cuantificar y entender de qué manera la transformación de ecosistemas y el cambio climático han impactado e impactarán estos procesos, se seleccionó un conjunto de pares de especies planta-animal que pudieran informar sobre procesos de dispersión y polinización. Se utilizaron datos bien soportados por evidencias con soporte bibliográfico, y sobre los que existieran datos de ocurrencia geográfica suficientes para producir buenos modelos espaciales de distribución.</p>

                        <p>El análisis de la pérdida/degradación de estos procesos se estimó a partir de los cambios (pérdida/incremento) de la coincidencia geográfica entre los pares de especies interactuantes, como se muestra en los mapas anexos.</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <h2 className="mb-5">Ecological Processes</h2>
                        <h3 className="subtitulo pl-3">Some interactions between animals and plants</h3>
                        <p className="mt-5">Among the many ecological processes (see Criterion D), mutualistic processes of plant-animal interactions such as pollination and seed dispersal play a fundamental role in maintaining ecosystem integrity, by conditioning other processes, such as fruiting, seed establishment, plant recruitment and vegetation regeneration  <span className="nota-al-pies-number" data-tip="<span>[15]</span>. Fontúrbel, F. E., Candia, A. B., Malebrán, J., Salazar, D. A., González-Browne, C., and Medel, R. (2015). Meta-analysis of anthropogenic habitat disturbance effects on animal-mediated seed dispersal. Global Change Biology, 21(11), 3951–3960. Available from: <a href='https://doi.org/10.1111/gcb.13025'>https://doi.org/10.1111/gcb.13025</a>" data-for="tooltip-procesos-ecologicos">[15]</span>. In the tropics, particularly in rainforest ecosystems, these processes are especially dominant and important for the functioning of ecosystems and their services. In these forest ecosystems up to 85 % of the trees depend on animals to fulfil their reproductive cycles, often by specific species <span className="nota-al-pies-number" data-tip="<span>[16]</span>. Neuschulz, E. L., Mueller, T., Schleuning, M. and Böhning-Gaese, K. (2016). Pollination and seed dispersal are the most threatened processes of plant regeneration. <em>Scientific Reports</em>, 6(29839), 1–6. doi: 10.1038/srep29839 " data-for="tooltip-procesos-ecologicos">[16]</span>.</p>

                        <p>To quantify and understand how the transformation of ecosystems and climate change have impacted and will impact these processes, a set of pairs of plant-animal species that could inform about seed dispersal and pollination processes was selected. Data well supported by evidence were used, and for which there were sufficient geographic occurrence data, to produce good spatial distribution models.</p>

                        <p>The analysis of the changes and degradation of these processes was estimated from the changes (loss/increase) of the geographical coincidence between the pairs of interacting species as shown in the attached maps.</p>
                    </>}

                <ReactTooltip
                    id="tooltip-procesos-ecologicos"
                    place="top"
                    html={true}
                    clickable={true}
                    effect={'solid'}
                    type={'info'}
                    event={'click'}
                    globalEventOff={'click'}
                    className={'nota-al-pie'}
                >
                </ReactTooltip>
            </>
        )
    }
    const state = useTrackedState();
    const { language } = state;
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();
    const refContenido = useRef();
    const refImagenes = useRef();
    const numImagenesScroll = 2;

    useEffect(() => {
        if (refContenido.current !== undefined) {
            setTimeout(() => {
                ScrollTrigger.create({
                    end: "bottom bottom",
                    // markers: true,
                    pin: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-procesos-ecologicos", refImagenes, ".imagenes-scrolll-procesos-ecologicos", numImagenesScroll, headerSpace).pin,
                    pinSpacing: false,
                    start: () => calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-procesos-ecologicos", refImagenes, ".imagenes-scrolll-procesos-ecologicos", numImagenesScroll, headerSpace).start,
                    trigger: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-procesos-ecologicos", refImagenes, ".imagenes-scrolll-procesos-ecologicos", numImagenesScroll, headerSpace).trigger,
                    invalidateOnRefresh: true
                })

            }, 200);
        }
    }, [refContenido.current])

    return (
        <div id="procesos-ecologicos">
            <div className="container-fluid row mx-0 justify-content-end py-5 align-items-start px-0">
                <div className="col-lg-6 pl-5 d-flex justify-content-lg-end">
                    <div
                        className="introduccion-textos introduccion-textos-procesos-ecologicos"
                        ref={refContenido}
                    >
                        <Textos />
                    </div>
                </div>

                <div
                    className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-procesos-ecologicos m-0"
                    ref={refImagenes}
                >
                    <div>
                        <img src={`./img/perdida_procesos_ecologicos/procesos_ecologicos_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
                    </div>
                    <div>
                        <img src={`./img/perdida_procesos_ecologicos/dispersion_semillas_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProcesosEcologicos;
