import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';


const BosquesHumedos = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;

  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>

            <p className="mt-lg-3">Bosque denso de más de 30 m de altura con cuatro estratos definidos. Dosel irregular. Árboles como <em>Brownea macrophylla</em>, <em>Brosimum alicastrum</em>, <em>Lonchocarpus hedyosmus</em>, <em>Pachira quinata</em>, <em>Chrysophyllum</em> sp., <em>Anacardium giganteum</em>, <em>Guarea</em> sp. Abundantes palmas (<em>Attalea butyracea</em>, <em>Euterpe precatoria</em>, <em>Socratea exorrhiza</em>) y lianas, con sotobosque denso. Tiene algunos elementos florísticos del bosque seco tropical (<em>Bursera</em>, <em>Hura</em>, <em>Tabebuia</em>). Topografía plana. Suelos generalmente profundos, bien drenados, ligeramente ácidos.</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-hidrocarburos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_hidrocarburos_3.png'})`, }}>Hidrocarburos</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Uso del suelo</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }}
                >Energía</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_1.png'})`, }}>Infraestructura vial</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}
                >Riesgo de incendios</li>
              </ul>
            </div>
            <div className="mb-lg-5">

              <img src='./img/estado_actual/bosques_humedos/precipitacion.jpg' alt="imagen de ambiente 1" className="img-fluid" />

            </div>
          </>
        }
        {language === 'en' &&
          <>
            <p className="mt-lg-3">Dense forest over 30 m high with four defined strata. Irregular canopy. It contains trees such as <em>Brownea macrophylla</em>, <em>Brosimum alicastrum</em>, <em>Lonchocarpus hedyosmus</em>, <em>Pachira quinata</em>, <em>Chrysophyllum</em> sp., <em>Anacardium giganteum</em>, <em>Guarea</em> sp. With abundant palms (<em>Attalea butyracea</em>, <em>Euterpe precatoria</em>, <em>Socratea exorrhiza</em>) and lianas, and a dense undergrowth. Contains some floristic elements of the tropical dry forest (<em>Bursera</em>, <em>Hura</em>, <em>Tabebuia</em>). Topografía plana. Flat topography. Generally deep, well drained, slightly acidic soils.</p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-hidrocarburos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_hidrocarburos_3.png'})`, }}>Fossil fuels extraction</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }}>Energy</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_1.png'})`, }}>Road infrastructure</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_humedos/precipitacion_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }

  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5" id="prueba-desplazar">
        <div className="col-lg-4 pl-5 pt-5">
          <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5">Bosques húmedos tropicales del piedemonte llanero</h3>
              </>
            }
            {language === 'en' &&
              <>
                <h3 className="mt-0 mt-lg-5">Tropical Humid Forests in the Llanos piedmont</h3>
              </>
            }

          </div>

          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
              <img src={`./img/estado_actual/bosques_humedos/info_bosques_humedos_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>

            <div className="col-lg-9 px-0">
              <MapaContainer
                img="./img/estado_actual/bosques_humedos/Mapas01.jpg"
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default BosquesHumedos;