import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';

const BosquesSubhumedosAltiplano = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;

  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>
            
            <p className="mt-lg-3">Bosques medios densos siempreverdes con hojas ocasionalmente coriáceas con dosel denso y regular de unos 10 a 15 m. Estratificación poco definida. Piso cubierto de musgos, helechos y microorquídeas. Epifitismo abundante de bromelias y orquídeas. Ente los árboles y arbustos característicos están  <em>Ilex kunthiana</em>, <em>Myrcianthes leucoxyla</em>, <em>Vallea stipularis</em>, <em>Juglans neotropica</em>, <em>Alnus acuminata</em>.</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_3.png'})`, }}
                >Infraestructura vial</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}
                >Energía</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Uso del suelo</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}
                >Riesgo de incendios</li>
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_1.png'})`, }}
                >Minería</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_subhumedos_altiplano/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>
           
            <p className="mt-lg-3">Dense medium evergreen forests occasionally with leathery leaves, with dense and regular canopy of about 10 to 15m. Stratification poorly defined. Floor covered with mosses, ferns and micro-orchids. Abundant epiphytism of bromeliads and orchids. Among the characteristic trees and shrubs are <em>Ilex kunthiana</em>, <em>Myrcianthes leucoxyla</em>, <em>Vallea stipularis</em>, <em>Juglans neotropica</em>, <em>Alnus acuminata</em>.</p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_3.png'})`, }}>Road infrastructure</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}>Energy</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk </li>
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_1.png'})`, }}>Mining</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_subhumedos_altiplano/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }

  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5">
        <div className="col-lg-4 pl-5 pt-5">
          <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5 py-3 py-lg-5">Bosques subhúmedos del altiplano cundiboyacense</h3>
              </>
            }
            {language === 'en' &&
              <>
                 <h3 className="mt-0 mt-lg-5 py-3 py-lg-5">Sub-Humid Forests of the Cundinamarca-Boyacá High-plain</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
              <img src={`img/estado_actual/bosques_subhumedos_altiplano/info_bosques_subhumedos_altiplano_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            <div className="col-lg-9 px-0 mapa-subhumedos-cundiboyacense">
              <MapaContainer
                img="./img/estado_actual/bosques_subhumedos_altiplano/Mapas06.jpg"
              />
            </div>     
          </div>
        </div>
      </div>
    </>
  )
}

export default BosquesSubhumedosAltiplano;