import React from 'react';
import { useLocation } from 'react-router-dom';
import { Link as ScrollLink} from 'react-scroll';
import normalizeString from '../js/utilities/normalizeString.js';
import { useTrackedState } from '../Store/index.js';

const MenuLateral = (props) => {
  const {section, subsections} = props;
  const pathnames = ['/', '/introduccion']
  const location = useLocation();
  const {pathname} = location;

  const state = useTrackedState();
  const { language } = state;

  return (
    <ul className="menu-lateral"
      id="menu-lateral"
      style={{left: pathnames.includes(pathname) ? '-70px' : 0}}
    >
      {subsections[language].map((item, index)=>
        <li key={`menu-lateral-${index}`}>
          <ScrollLink
            className="menu-lateral-element" 
            activeClass="menu-lateral-active" 
            to={`${section}-${normalizeString(item)}`} 
            offset={-80} 
            spy={true} 
            smooth={true} 
            duration={500}>
              <span>{item}</span>
          </ScrollLink>
        </li>
      )}
    </ul>
  )
  
}

export default MenuLateral;