import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';


const BosquesHumedosTropicales = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;
  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>
            <p className="mt-lg-3">Bosques densos de 10 a 15 m con dosel denso en topografía quebrada con hojas pequeñas y coriáceas (<em>Weinmannia tomentosa</em>, <em>Clusia</em> sp., <em>Miconia</em> spp., <em>Piper bogotense</em>, <em>Palicourea</em> sp., <em>Oreopanax</em> sp., <em>Rapanea</em> sp.). Contrastan cañadas con árboles de gran tamaño (<em>Cedrela montana</em>, <em>Nectandra</em> sp.,<em>Cordia</em> sp.), y ocasionales palmas de cera (<em>Ceroxylon</em> sp.). En laderas, piso cubierto de tapete de raíces finas con musgos, líquenes, helechos y microorquídeas.</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}>Energía</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_3.png'})`, }}>Infraestructura vial</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Uso del suelo</li>
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_1.png'})`, }}>Minería</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Riesgo de incendios</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_subhumedos_tropicales/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>
            <p className="mt-lg-3">Dense forests of about 10 to 15 m with dense canopy in hilly topography with small and leathery leaves (<em>Weinmannia tomentosa</em>, <em>Clusia</em> sp., <em>Miconia</em> spp., <em>Piper bogotense</em>, <em>Palicourea</em> sp., <em>Oreopanax</em> sp., <em>Rapanea</em> sp.). Ravines with large trees (<em>Cedrela montana</em>, <em>Nectandra</em> sp.,<em>Cordia</em> sp.), and occasional wax palms (<em>Ceroxylon</em> sp.). Floor densely covered with fine root mat with abundant mosses, lichens, ferns and micro-orchids.</p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}
>Energy</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_3.png'})`, }}
>Road infrastructure </li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Agriculture</li>
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_1.png'})`, }}>Mining</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_subhumedos_tropicales/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }
  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5">
        <div className="col-lg-4 pl-5 pt-5">
        <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5">Bosques subhúmedos tropicales altoandinos</h3>   
              </>
            }
            {language === 'en' &&
              <>
                <h3 className="mt-0 mt-lg-5">Sub-Humid Tropical Forests of the Andean Highlands</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
              <img src={`img/estado_actual/bosques_subhumedos_tropicales/info_bosques_subhumedos_tropicales_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            <div className="col-lg-9 px-0">
              <MapaContainer
                  img="./img/estado_actual/bosques_subhumedos_tropicales/Mapas05.jpg"
                />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default BosquesHumedosTropicales;