import React from "react";
import { useTrackedState } from '../../../Store';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';

const PortadaBosquesSubhumedosAltiplano = () => {
    const state = useTrackedState();
    const {language} = state;
    const [heightContainer, containerRef] = useGeneralHeightContainer();
    return(
      <div 
        ref={containerRef}
        style={{height: `${heightContainer}px`}}
        className="height-responsive"
      >
        <div className="container d-flex  justify-content-center h-100  height-responsive pt-5 pt-lg-0 pl-5 pl-lg-0">
          <div className="text-center">
            <img 
              src='img/estado_actual/bosques_subhumedos_altiplano/portada2.jpg' 
              alt="imagen de Bosques subhúmedos del altiplano cundiboyacense" 
              className="img-portada"
            />
            {language === 'es' &&
              <h3 >Bosques subhúmedos <br/>del altiplano cundiboyacense</h3>
            }
            {language === 'en' &&
              <h3 >Sub-Humid Forests of the<br/>Cundinamarca-Boyacá High-plain</h3>
            }
          </div>
        </div>
      </div>
    )
  }

  export default PortadaBosquesSubhumedosAltiplano;