import React, { useEffect } from "react";

import { gsap } from 'gsap/gsap-core';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import EspacioInferior from "./EspacioInferior";

import useIsMobile from "../hooks/useIsMobile";


gsap.registerPlugin(ScrollTrigger);
gsap.core.globals('ScrollTrigger', ScrollTrigger);
const CambioImagenScroll = (props) => {
    const { carpeta, claseMapa, idioma, nombreImagen, numMapas } = props;
    const isMobile = useIsMobile();

    useEffect(() => {
        setTimeout(() => {
            gsap.utils.toArray(`.${claseMapa}`).forEach((imagen, i) => {
                gsap.set(imagen, { opacity: 0 })

                ScrollTrigger.matchMedia({
                    "all": function () {
                        ScrollTrigger.defaults({
                            trigger: imagen,
                            end: () => endImagenes(imagen),
                            start: () => cambiarEspacioStart(imagen),
                            pin: true,
                            pinSpacing: false,
                            // markers: true,
                            onEnter: () => {
                                gsap.set(imagen, { opacity: 1 })
                            },
                            onEnterBack: () => {
                                gsap.set(imagen, { opacity: 1 })
                            },
                        });
                        if (i === 0) {
                            gsap.set(imagen, { opacity: 1 })
                            ScrollTrigger.create({
                                onLeave: () => {
                                    gsap.set(imagen, { opacity: 0 })
                                },
                            })
                        }
                        else if (i === numMapas - 1) {
                            ScrollTrigger.create({
                                end: bottomUltimaImagen(),
                                onLeaveBack: () => {
                                    gsap.set(imagen, { opacity: 0 })
                                }
                            })
                        }
                        else {
                            ScrollTrigger.create({
                                onLeave: () => {
                                    gsap.set(imagen, { opacity: 0 })
                                },
                                onLeaveBack: () => {
                                    gsap.set(imagen, { opacity: 0 })
                                }
                            })
                        }
                    }
                })

                // ScrollTrigger.refresh();
                ScrollTrigger.update();
            })
        }, 200);


        return () => {
            ScrollTrigger.getAll().forEach(ST => ST.disable());
        }
    }, [])

    const cambiarEspacioStart = (imagen) => {
        var espacioStart = (window.innerHeight - imagen.offsetHeight) / 2;
        return `top +=${espacioStart}px`
    }

    const bottomUltimaImagen = () => {
        var altoMenu = document.getElementById('menu').getBoundingClientRect().height;
        var espacioInferior = document.getElementsByClassName('espacio-inferior')[0].getBoundingClientRect().height;
        var botonDescarga = document.getElementsByClassName("contenedor-btn-descarga")[0] !== undefined ? document.getElementsByClassName("contenedor-btn-descarga")[0].offsetHeight : 0;

        var valorBottom;
        if (!isMobile) {
            valorBottom = botonDescarga !== 0 ? window.innerHeight / 2 + window.innerHeight / 4 +  espacioInferior : window.innerHeight / 2 + altoMenu + espacioInferior ;
        } else {
            valorBottom = window.innerHeight / 2 + window.innerHeight / 4;
        }
        return `bottom +=${valorBottom}px`
    }


    const endImagenes = (imagen) => {
        var espacioInferior = document.getElementsByClassName('espacio-inferior')[0].getBoundingClientRect().height;        
        var espacioStart = (window.innerHeight - imagen.offsetHeight) / 2;

        var espacioEnd = isMobile ? `bottom +=${window.innerHeight/ 4 +espacioInferior}px` : `bottom +=${espacioStart}px`
        return espacioEnd;
    }


    return (
        <>
            {
                (() => {
                    var imagen = []
                    for (var i = 0; i < numMapas; i++) {
                        var num = i !== numMapas ? i + 1 : numMapas;
                        imagen.push(
                            <div
                                className={claseMapa}
                                key={nombreImagen + i}
                            >
                                <img className="w-100" alt={nombreImagen} src={`./img/${carpeta}/${nombreImagen}_0${num}_${idioma}.jpg`} />
                            </div>
                        )
                    }
                    return imagen;
                })()
            }
            <EspacioInferior />
            <EspacioInferior />
        </>
    )
}

export default CambioImagenScroll;
