import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';

const SabanasPiedemonte = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;
  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>
            
            <p className="mt-lg-3">Sabanas herbáceas macolladas (<em>Trachypogon</em> spp., <em>Paspalum</em> spp.), con pequeños arbustos y arbolitos dispersos (<em>Curatella americana</em>, <em>Bowdichia virgilioides</em>), y algunas matas de monte circulares asociadas a hormigueros. Atravesadas por pequeñas franjas de bosques de morichales (<em>Mauritia flexuosa</em>) y bosquecitos de galería..</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-hidrocarburos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_hidrocarburos_3.png'})`, }}
                >Hidrocarburos</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Uso del suelo</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }}
                >Energía</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_1.png'})`, }}>Infraestructura vial</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>
                  Riesgo de incendios</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/sabanas_piedemonte/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>
            
            <p className="mt-lg-3">Herbaceous tussock savannas (<em>Trachypogon</em> spp., <em>Paspalum</em> spp.), with small shrubs and scattered trees (<em>Curatella americana</em>, <em>Bowdichia virgilioides</em>), and circular bushlands associated with anthills. Crossed by small strips of gallery forests notably “morichales” (<em>Mauritia flexuosa</em>).</p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-hidrocarburos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_hidrocarburos_3.png'})`, }}>Fossil fuels extraction</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }}>Energy</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_1.png'})`, }}>Road infrastructure</li>
                <li  className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/sabanas_piedemonte/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }
  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5">
        <div className="col-lg-4 pl-5 pt-5">
          <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5">Sabanas del piedemonte llanero</h3>
              </>
            }
            {language === 'en' &&
              <>
                <h3 className="mt-0 mt-lg-5">Savannas of the Llanos Piedmont</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            {/* {language === 'es' &&
              <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
                <img src='img/estado_actual/sabanas_piedemonte/mapa_info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
              </div>}

            {language === 'en' &&
              <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
                <img src='img/estado_actual/sabanas_piedemonte/mapa_info_eng.jpg' alt="imagen de ambiente 1" className="img-fluid" />
              </div>} */}

            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
              <img src={`img/estado_actual/sabanas_piedemonte/info_sabanas_piedemonte_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            <div className="col-lg-9 px-0">
              <MapaContainer
                  img="./img/estado_actual/sabanas_piedemonte/Mapas04.jpg"
                />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default SabanasPiedemonte;