import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';

const HumedalesAltiplano = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;
  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>
            <p className="mt-lg-3">Mosaico en franjas de bosques medios densos (<em>Alnus acuminata</em>, <em>Miconia reclinata</em>), pasando por arbustales (<em>Baccharis revoluta</em>, <em>Eupatorium fastigiatum</em>, <em>Myrica</em> sp.), hasta vegetación de juncos y otras plantas acuáticas (<em>Scirpus californicus</em>, <em>Typha</em> sp.) bordeando espejos de agua. Topografía plano-cóncava. Asociados a las llanuras de inundación recientes y subrecientes de los ríos Bogotá, Chulo, Subachoque y Suárez.</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}
                >Energía</li>
                <li className="principales-amenazas-remocion pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_remocion_2.png'})`, }}
                >Remoción en masa</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_2.png'})`, }}>Infraestructura vial</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_1.png'})`, }}>Uso del suelo</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Riesgo de incendios</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/humedales_altiplano/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>
            <p className="mt-lg-3">Mosaic with fringes of dense medium forests (<em>Alnus acuminata</em>, <em>Miconia reclinata</em>), associated with shrublands (<em>Baccharis revoluta</em>, <em>Eupatorium fastigiatum</em>, <em>Myrica</em> sp.), and herbaceous vegetation with reeds and other aquatic plants (<em>Scirpus californicus</em>, <em>Typha</em> sp.) bordering enclosed and flowing water bodies. Flat-concave topography. Associated with river flood plains of the Bogotá, Chulo, Subachoque, and Suárez rivers.</p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}>Energy</li>
                <li className="principales-amenazas-remocion pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_remocion_2.png'})`, }}>Massive removal</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_2.png'})`, }}>Road infrastructure</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_1.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/humedales_altiplano/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }
  return (
    <>
      <div className="row mx-auto pt-5 align-items-start pl-lg-5 pb-5 pb-lg-0">
        <div className="col-lg-4 pl-5 pt-5">
        <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                  <h3 className="mt-0 mt-lg-5">Humedales del altiplano de la cordillera Oriental</h3>
              </>
            }
            {language === 'en' &&
              <>
                 <h3 className="mt-5 py-5mt-0 mt-lg-5">Wetlands of the Eastern Cordillera high-plain</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
              <img src={`img/estado_actual/humedales_altiplano/info_humedales_altiplano_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            <div className="col-lg-9 px-0">
              <MapaContainer
                img="./img/estado_actual/humedales_altiplano/Mapas10.jpg"
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default HumedalesAltiplano;