import React, { useCallback, useState } from 'react';
import { useLocation, Link } from 'react-router-dom';
import { useDispatch, useTrackedState } from '../Store';
import MenuButton from "./BtnMenuResponsive";
import Menu from './Menu';

const Header = () => {
  const dispatch = useDispatch();
  const state = useTrackedState();
  const { language, title, languages } = state;
  const [headerDimentions, setHeaderDimentions] = useState({ height: 0, top: 0 })
  const { height, top } = headerDimentions;
  const location = useLocation()
  const header = useCallback(node => {
    const pathnames = ['/', '/introduccion']
    const createHeight = () => {
      if (node !== null) {
        let height = node.getBoundingClientRect().height
        setHeaderDimentions({
          height: height,
          top: pathnames.includes(location.pathname) ? -(height) : 0
        })
      }
    }
    createHeight();
    window.addEventListener('resize', createHeight)
    return () => {
      window.removeEventListener('resize', createHeight)
    }
  }, [location])

  const [open, setOpen] = useState(false)

  const handleClick = () => {
    setOpen(!open)
  }
  return (
    <>

      <header className="row mx-0 justify-content-between align-items-center p-2 px-lg-4"
        id={'main-header'}
        ref={header}
        style={{
          top: `${top}px`,
          background: `url(${'./img/fondo_header.jpg'}) center / cover no-repeat`
        }}
      >
        <MenuButton open={open} handleClick={handleClick} />

        <>{title[language]()}</>

        <ul className="menu-language d-none d-lg-block">
          {languages.map((item, index) =>
            <li
              className={`${item === language ? 'language-active' : ''}`}
              key={`language${index}`}
              onClick={() => {
                dispatch({
                  type: 'SET_GENERAL_STATE',
                  key: 'language',
                  value: item
                })
              }}
            >
              {item}
            </li>
          )}
        </ul>
      </header>
      <Menu open={open} handleClick={setOpen} />
      {open &&
        <div className="cerrarMenu" onClick={() => { setOpen(false) }}></div>
      }
      <div className="header-space"
        style={{
          height: `${height}px`,
          marginTop: `${top}px`
        }}
      ></div>


    </>
  )
}

export default Header;