import React, { useEffect, useRef, useState } from 'react';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import calcularPropiedadesScrollTrigger from '../../js/utilities/calcularPropiedadesScrollTrigger';
import CambioImagenScroll from '../../components/CambioImagenScroll';
import DownloadButton from '../../components/Btn-descarga';

const PerdidaEcosistemas = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                {language === 'es' &&
                    <>
                        <h2>Pérdida de ecosistemas</h2>
                        <p>La historia de la intervención humana ha dejado su impronta con la degradación y desaparición de grandes áreas de ecosistemas del país.</p>
                        <p>Actualmente subsiste a nivel nacional un 66 % de los ecosistemas naturales. Sin embargo, existen grandes diferencias a nivel regional, pues en la región caribe y andina estos valores se reducen al 32 % y al 42 %, respectivamente.</p>
                        <p>Por otra parte, si analizamos únicamente los ecosistemas de bosques originales, que ocupaban más del 80 % del país (93 millones de hectáreas), subsiste el 63 %.</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <h2>Loss of Ecosystems</h2>
                        <p>The history of human activities on the ecosystems has left an important footprint, leading in many cases to the degradation and disappearance of significant areas of ecosystems in the country.</p>
                        <p>Currently, 66 % of the area of its natural ecosystems subsists nationally. However, there are large regional differences, as in the Caribbean and Andean regions, for example, these values ​​have been reduced to 32 % and 42 %, respectively.</p>
                        <p>On the other hand, if we analyze only the original forest ecosystems which occupied more than 80 % of the country (93 million hectares), about 63 % of their area remains.</p>
                    </>}
            </>
        )
    }

    const state = useTrackedState();
    const { language } = state;
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();

    const refContenido = useRef();
    const refImagenes = useRef();

    const [positionBoton, setPositionBoton]= useState();

    const numImagenesScroll = 2;

    useEffect(() => {
        if (refContenido.current !== undefined) {
            setTimeout(() => {
                var scroll = ScrollTrigger.create({
                    end: "bottom bottom",
                    // markers: true,
                    pin: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-perdida-ecosistemas", refImagenes, ".imagenes-perdida-ecosistemas", numImagenesScroll, headerSpace).pin,
                    pinSpacing: false,
                    start: () => calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-perdida-ecosistemas", refImagenes, ".imagenes-perdida-ecosistemas", numImagenesScroll, headerSpace).start,
                    trigger: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-perdida-ecosistemas", refImagenes, ".imagenes-perdida-ecosistemas", numImagenesScroll, headerSpace).trigger,
                    invalidateOnRefresh: true,
                    onEnter: () => {
                        setPositionBoton("fixed");
                    },
                    onEnterBack:()=>{
                        setPositionBoton("fixed");
                    },
                    onLeave: () => {
                        setPositionBoton("relative");
                    },
                    onLeaveBack: () => {
                        setPositionBoton("relative");
                    },
                })

            }, 200);

        }
    }, [])



    return (

        <div id="perdida-de-ecosistemas1">
            <div className="container-fluid row mx-0 justify-content-end align-items-start pt-5 px-0">
                <div className="col-lg-6 pl-5 d-flex justify-content-lg-end">
                    <div
                        className="introduccion-textos introduccion-textos-perdida-ecosistemas"
                        ref={refContenido}
                    >
                        <Textos />
                    </div>
                </div>

                <div
                    className="col-lg-8 px-0 imagenes-scroll imagenes-scroll-perdida imagenes-perdida-ecosistemas m-0"
                    ref={refImagenes}
                >

                    <CambioImagenScroll
                        carpeta="perdida_ecosistema"
                        claseMapa="img-mapa"
                        idioma={language}
                        linkDescarga="https://drive.google.com/drive/folders/1fiqLZ5MTEroIsqOHSjwgYPPSMRTeButL"
                        nombreImagen="perdida_ecosistema"
                        numMapas={numImagenesScroll}
                        tieneBoton={true}
                    />
                    <div className="d-none">
                        <CambioImagenScroll
                            carpeta="perdida_ecosistema"
                            claseMapa="img-mapa-2"
                            idioma={language}
                            nombreImagen="perdida_ecosistema"
                            numMapas={numImagenesScroll}
                        />
                    </div>
                </div>
            </div>
            <DownloadButton
                linkDescarga="https://drive.google.com/drive/folders/1fiqLZ5MTEroIsqOHSjwgYPPSMRTeButL"
                position={positionBoton}
                tieneMargin={true}
            />
        </div>
    )

}

export default PerdidaEcosistemas;