import React from 'react';
import {Element} from 'react-scroll';
//Componentes
import FadeElement from '../../components/FadeElement';
import MenuLateral from '../../components/MenuLateral';
import Autores from './Autores';
import Creditos from './Creditos';
import EspacioInferior from '../../components/EspacioInferior';
import { useTrackedState } from '../../Store';
import normalizeString from '../../js/utilities/normalizeString';

const Creditos_App = () => {

  const state = useTrackedState();
  const { language } = state;
  
  const secciones = {
    es: ['Autores', 'Créditos'],
    en: ['Authors', 'Credits']
  }

  return(
    <>
      <MenuLateral 
        section={'creditos'}
        subsections={secciones}
      />
      <Element name={`creditos-${normalizeString(secciones[language][0])}`}>
        <Autores/>
      </Element>
      <Element name={`creditos-${normalizeString(secciones[language][1])}`}>
        <Creditos/>
      </Element>
      <FadeElement/>
      <EspacioInferior/>
    </>
  )
}

export default Creditos_App;