import React, { useEffect, useRef, useState } from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';

const BosquesSubhumedos = () => {

  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>

            <p className="mt-lg-3">Bosque de hasta 20-25 m con dosel irregular de árboles siempreverdes (<em>Billia</em> sp., <em>Lecythis</em> sp., <em>Dialyanthera</em> sp., <em>Ochroma</em> sp.) y caducifolios (<em>Tabebuia</em> spp. <em>Bombax</em> sp., <em>Cedrela odorata</em>). Estratificación poco definida con abundantes lianas y con un sotobosque arbustivo denso con algunas lianas. Palmas conspicuas que llegan al dosel (<em>Dictyocaryum</em> sp., <em>Socratea</em> sp.,). Topografía colinada a montañosa.</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_3.png'})`, }}
                >Minería</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }}
                >Energía</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Uso del suelo</li>
                <li className="principales-amenazas-cultivos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_cultivos_1.png'})`, }}
                >Cultivos ilícitos</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}
                >Riesgo de incendios</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_subhumedos/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>

            <p className="mt-lg-3">Forest up to 20-25 m with irregular canopy of evergreen trees (<em>Billia</em> sp., <em>Lecythis</em> sp., <em>Dialyanthera</em> sp., <em>Ochroma</em> sp.) and deciduous species (<em>Tabebuia</em> spp. <em>Bombax</em> sp., <em>Cedrela odorata</em>). Poorly defined stratification with abundant lianas, and with a dense shrubby undergrowth. Conspicuous palms that reach the canopy (<em>Dictyocaryum</em> sp., <em>Socratea</em> sp.,). Topography of hills and mountains with steep slopes.</p>
            <div>
              <p><strong>Main Threats</strong></p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_3.png'})`, }}>Mining</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }}>Energy</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-cultivos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_cultivos_1.png'})`, }}>Illegal crops</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_subhumedos/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }


  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;


  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5 estadoactual-bosques-subhumedos-tropicales-del-caribe-andes">
        <div className="col-lg-4 pl-5 pt-5">
          <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5">Bosques subhúmedos tropicales del Caribe-Andes</h3>
              </>
            }
            {language === 'en' &&
              <>
                <h3 className="mt-0 mt-lg-5">Caribbean-Andean Tropical Sub-humid Forests</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 px-3 px-lg-0 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute">
              <img src={`./img/estado_actual/bosques_subhumedos/info_bosques_subhumedos_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            <div className="col-lg-9 px-0 mapa-bosques-subhumedos-ca">
              <MapaContainer
                centrar={true}
                className="img-fluid"
                img="./img/estado_actual/bosques_subhumedos/Mapas02.jpg"
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default BosquesSubhumedos;