import React from 'react';
// import {useTrackedState }  from '../../Store';
import { useDispatch, useTrackedState }  from '../../Store';
import ScrollAIntroduccion from './ScrollAIntroduccion';

const Portada = () => {
  const dispatch = useDispatch();
  const state = useTrackedState();
  // const {language} = state;
  const {language, languages} = state;
  return(
    <div className="portada row mx-0"
      style={{
        backgroundImage: `url(${'./img/portada.jpg'})`
      }}
    >
      <div className="idiomas py-3 py-lg-0">
        <ul className="menu-language">
          {languages.map((item, index)=>
            <li 
              className={`${item === language ? 'language-active':''}`}
              key={`language${index}`}
              onClick={()=>{
                dispatch({
                  type:'SET_GENERAL_STATE', 
                  key:'language', 
                  value: item
                })
              }}
            >
              {item}
            </li>
          )}
        </ul>
      </div>

      <div className="title-container">
        {language === 'es' &&
          <>
            <h1 className="text-center text-lg-left">Ecosistemas colombianos</h1>
            <h3 className="text-center text-lg-left">Amenazas y riesgos</h3>
            <p className="description py-4 py-lg-0 text-center text-lg-left">Una aplicación de la Lista Roja de Ecosistemas a los ecosistemas terrestres continentales</p>
            <p className="autores text-center text-lg-left">Andrés Etter, Angela Andrade, Kelly Saavedra, <br/>Paula Amaya, Juliana Cortés y Paulo Arévalo</p>
        
          </>
        }
        {language === 'en' &&
           <>
           <h1 className="text-center text-lg-left">Colombian Ecosystems</h1>
           <h3 className="text-center text-lg-left">Threats and Risks</h3>
           <p className="description">An application of the Red List of Ecosystems to the continental terrestrial ecosystems</p>
           <p className="autores text-center text-lg-left">Andrés Etter, Angela Andrade, Kelly Saavedra, <br/>Paula Amaya, Juliana Cortés, and Paulo Arévalo</p>
        
         </>
        }
       
      </div>

      <div className="logo-red-list">
        <img src='./img/red_list_logo.png'alt="logo red list"/> 
        <img className="editorial" src="./img/editorial_javeriana.png" alt="logo de la editorial javeriana" /> 
      </div>  

      <div className="mapa-container">
        <img src="./img/mapa.png" alt="mapa colombia" className="img-fluid"/> 
      </div>  

      <button className="flecha-abajo general-button"
        onClick={()=>{
          ScrollAIntroduccion();
        }}
        style={{
          backgroundImage: `url(${'./img/flecha-abajo.svg'})`
        }}
      ></button>  
    </div>
  )
}

export default Portada;