import React, { useEffect } from 'react';
import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import MapaContainer from '../../components/MapaContainer';

const Amenazas = () => {
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                {language === 'es' &&
                    <>

                        <h3 className="subtitulo pl-3 mt-3">Niveles de intensidad de las amenazas a los ecosistemas por sectores productivos y su relación con los niveles de riesgo</h3>
                    </>
                }
                {language === 'en' &&
                    <>

                        <h3 className="subtitulo pl-3 mt-3">Threat intensity levels of the ecosystems for each productive sector and its relation to the risk levels</h3>
                    </>
                }
            </>
        )
    }

    const state = useTrackedState();
    const { language } = state;
    useEffect(() => {
    }, [language]);

    return (
        <div className="row mx-auto py-lg-5 align-items-start pl-lg-5 mb-5">
            <div className="row mx-auto py-lg-5 align-items-start">
                <div className="col-lg-4 pl-5 pt-lg-5">

                    <div className="title-sticky"
                        style={{
                            top: `${headerSpace}px`
                        }}
                    >
                        {language === 'es' &&
                            <>
                                <h2>Amenazas</h2>
                            </>
                        }
                        {language === 'en' &&
                            <>
                                <h2>Threats</h2>
                            </>
                        }

                    </div>
                    <Textos />
                </div>
                <div className="col-lg-10 px-0"
                    style={{
                        top: 0
                    }}
                >
                    <div
                        ref={containerRef}
                        style={{ height: `${heightContainer}px` }}
                        className="d-lg-flex flex-column align-items-center justify-content-center  height-responsive mx-5 mx-lg-0"
                    >
                        <MapaContainer
                            img={`./img/estado_actual/amenazas_${language}.jpg`}
                            className="container-imagen-completa"
                        />
                    </div>

                </div>
            </div>
        </div >
    )
}

export default Amenazas;