import React, { useEffect } from 'react';
import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';

const EstadoEcosistemas = () => {
  // const [heightContainer, containerRef, headerSpace] = useHeightContainer();
  const [heightContainer, containerRef, headerSpace] = useHeightContainer();
  const Textos = () => {
    const state = useTrackedState();
    const { language } = state;
    return (
      <>
        {language === 'es' &&
          <>
            <img src="img/introduccion/estado_actual.png" className="mt-lg-4 mb-lg-2" alt="signo de admiración" />
            <p><strong>La aplicación de la Lista Roja de Ecosistemas a los ecosistemas terrestres continentales de Colombia indica que cerca del 25 % de estos (20 tipos) presentan niveles críticos de riesgo, lo que impacta de manera grave a la biodiversidad y los servicios ecosistémicos que estos soportan y a la población humana que depende de ellos. De estos ecosistemas en estado de alto riesgo han desaparecido más de 18 millones de hectáreas, al punto que su área remanente en la actualidad solo conforma un 3 % del área total de ecosistemas no transformados del país.</strong></p>

            <p><strong>Este diagnóstico muestra la urgente necesidad de desarrollar acciones que permitan frenar y revertir esta tendencia y que posibiliten encaminar un proceso de desarrollo del país que garantice modelos de uso sostenibles en el largo plazo.</strong></p>
          </>
        }
        {
          language === 'en' &&
          <>
            <img src="img/introduccion/estado_actual.png" className="mt-lg-4 mb-lg-2" alt="signo de admiración" />
            <p><strong>This first application of the Red List of Ecosystems to the continental terrestrial ecosystems in Colombia indicates that about 25 % of these (20 ecosystem types) present critical risk levels, seriously impacting the biodiversity and ecosystem services they support, upon which the Colombian and other human populations depend. Of these high-risk ecosystems, more than 18 million hectares of their original extent have disappeared, to the point that their current remnant area only constitutes 3 % of all ​​remaining natural ecosystems in the country.</strong></p>

            <p><strong>These figures call for the urgent need to develop actions that can curb and reverse this trend, to assure that the direction of the future developments of the country, are based on sustainable long-term use models.</strong></p>
          </>
        }
      </>
    )

  }
  const state = useTrackedState();
  const { language } = state;
  useEffect(() => {
  }, [language]);
  return (
    <>
      <div className="container-fluid row mx-0 py-5 position-relative">
        <div className="col-lg-6 pl-5 d-flex justify-content-center px-5">

          <div className="introduccion-textos">
            <div className="title-sticky"
              style={{
                top: `${headerSpace}px`
              }}
            >
              {language === 'es' &&
                <>
                  <h2>Riesgo general de ecosistemas</h2>
                </>
              }
              {language === 'en' &&
                <>
                  <h2>General Ecosystem Risk</h2>
                </>
              }

            </div>
            <div className="introduccion-textos introduccion">
              <Textos />
            </div>

          </div>
        </div>
        <div className="col-lg-8 px-0"
        /*  style={{
           position: 'sticky',
           top: `${headerSpace}px`
         }} */
        >
          {language === 'es' &&
            <div
              ref={containerRef}
              style={{ height: `${heightContainer}px` }}
              className="d-block d-flex d-lg-flex align-items-center justify-content-center height-responsive"
            >
              <img src='img/introduccion/aplicacion_lista_es.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          }

          {language === 'en' &&
            <div
              ref={containerRef}
              style={{ height: `${heightContainer}px` }}
              className="d-block d-flex d-lg-flex align-items-center justify-content-center height-responsive"
            >
              <img src='img/introduccion/aplicacion_lista_en.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          }


        </div>
      </div>
    </>
  )
}

export default EstadoEcosistemas;