import React, { useEffect, useRef } from 'react';
import ReactTooltip from 'react-tooltip';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import calcularPropiedadesScrollTrigger from '../../js/utilities/calcularPropiedadesScrollTrigger';

import MapaContainer from '../../components/MapaContainer';


const ConservacionAreasProtegidas = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        useEffect(() => {
            ReactTooltip.rebuild();
        }, [language]);
        return (
            <>
                {language === 'es' &&
                    <>
                        <div className="title-fixed"
                            style={{
                                top: `${headerSpace}px`
                            }}
                        >
                            <div className="row mx-0 ">
                                <div className="col-lg-8 px-0">
                                    <h2>Conservación y áreas protegidas nacionales</h2>
                                </div>
                                <div className="col-6 col-lg-4 px-0">
                                    <img src={`./img/conservacion_areas/guayacan.jpg`} alt="guayacan" className="img-fluid" />
                                </div>
                            </div>
                            <p>Los sistemas de áreas protegidas son una de las principales estrategias mediante las cuales los gobiernos y la sociedad civil buscan asegurar la conservación del patrimonio natural y cultural de la humanidad. El estándar global de conservación de la biodiversidad meta Aichi, establecido por la Convención de Diversidad Biológica, plantea la necesidad de proteger al menos el 17 % de los ecosistemas globales <span className="nota-al-pies-number" data-tip="<span>[4]</span>. Convention of Biological Diversity (CBD). (2010). <em>Strategic Plan for Biodiversity 2011-2020 and the Aichi Biodiversity Targets</em>. Recuperado de <a href='https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf'> https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf </a>" data-for="tooltip-conservacion-areas-protegidas">[4]</span>. Al ser la LRE un estándar internacional unificado que analiza las vulnerabilidades y amenazas de los ecosistemas, se convierte en herramienta que permite evaluar hasta qué punto los sistemas nacionales cumplen con estos requisitos e identificar los vacíos de conservación para apoyar la priorización que oriente la creación de nuevas áreas que permitan alcanzar las metas globales de conservación, y que represente adecuadamente la diversidad y la extensión de sus ecosistemas. De acuerdo con esta evaluación de la LRE para Colombia, en la actualidad el desempeño del Sistema Nacional de Áreas Protegidas debe mejorarse, ya que 52 (65 %) de los ecosistemas aquí identificados no cumplen este estándar.</p>
                        </div>
                    </>
                }
                {language === 'en' &&
                    <>
                        <div className="title-fixed"
                            style={{
                                top: `${headerSpace}px`
                            }}
                        >
                            <div className="row mx-0 ">
                                <div className="col-lg-8 px-0">
                                    <h2>Conservation and National Protected Areas</h2>
                                </div>
                                <div className="col-6 col-lg-4 px-0">
                                    <img src={`./img/conservacion_areas/guayacan.jpg`} alt="guayacan" className="img-fluid" />
                                </div>
                            </div>

                            <p className="mt-lg-3">Protected Areas systems are one of the main strategies through which governments and civil society seek to ensure the conservation of the natural and cultural heritage of humanity. The global standard of biodiversity conservation Meta Aichi established by the Convention on Biological Diversity, pledges to protect at least 17 % of global ecosystems <span className="nota-al-pies-number" data-tip="<span>[4]</span>. Convention of Biological Diversity (CBD). (2010). <em>Strategic Plan for Biodiversity 2011-2020 and the Aichi Biodiversity Targets</em>. Available from:<a href='https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf'> https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf </a>" data-for="tooltip-conservacion-areas-protegidas">[4]</span>. As the RLE is a unified international standard that analyzes the vulnerabilities and threats of ecosystems, it becomes a tool that allows to assess the extent to which national systems meet these requirements, and to identify conservation gaps to support the prioritization that guides creation of new areas that allow achieving global conservation goals, and that adequately represent the diversity and extent of its ecosystems. According to this evaluation of the RLE, the performance of the Colombian National System of Protected Areas must be improved, since 52 (65 %) of the ecosystems identified here do not meet this standard.</p>
                        </div>

                    </>}

                <ReactTooltip
                    id="tooltip-conservacion-areas-protegidas"
                    place="top"
                    html={true}
                    clickable={true}
                    effect={'solid'}
                    type={'info'}
                    event={'click'}
                    globalEventOff={'click'}
                    className={'nota-al-pie'}
                >
                </ReactTooltip>


            </>
        )
    }
    const state = useTrackedState();
    const { language } = state;
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();
    const refContenido = useRef();
    const refImagenes = useRef();
    const numImagenesScroll = 2;

    useEffect(() => {
        if(refContenido.current!== undefined){
            setTimeout(() => {
                ScrollTrigger.create({
                    end: "bottom bottom",
                    // markers: true,
                    pin: calcularPropiedadesScrollTrigger(refContenido, ".texto-conservacion-areas", refImagenes, ".imagenes-scrolll-conservacion-areas", numImagenesScroll, headerSpace).pin,
                    pinSpacing: false,
                    start: () => calcularPropiedadesScrollTrigger(refContenido, ".texto-conservacion-areas", refImagenes, ".imagenes-scrolll-conservacion-areas", numImagenesScroll, headerSpace).start,
                    trigger: calcularPropiedadesScrollTrigger(refContenido, ".texto-conservacion-areas", refImagenes, ".imagenes-scrolll-conservacion-areas", numImagenesScroll, headerSpace).trigger,
                    invalidateOnRefresh: true
                }) 
            }, 200);
        }
    }, [refContenido.current])




    return (
        <div id="conservacion-y-areas-protegidas-nacionales">
            <div className="container-fluid row mx-0 justify-content-end align-items-start pt-5 px-0">
                <div 
                    className="col-lg-6 pl-5 d-flex justify-content-lg-end"
                    ref={refContenido}
                >
                    <div className="introduccion-textos texto-conservacion-areas">
                        <Textos />
                    </div>
                </div>

                <div 
                    className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-conservacion-areas m-0"
                    ref={refImagenes}
                >
                    <div
                        className="d-flex flex-column align-items-center justify-content-center position-relative height-responsive mx-4 mx-lg-0 my-3 my-lg-5"
                    >
                        <MapaContainer
                            img={`./img/conservacion_areas/conservacion_areas_${language}.jpg`} className="container-imagen-completa pl-5 "
                        />
                    </div>

                    <div
                        className="d-flex flex-column align-items-center justify-content-center position-relative height-responsive mx-4 mx-lg-0 my-3 my-lg-5"
                    >
                        <MapaContainer
                            img={`./img/conservacion_areas/conservacion_areas_02_${language}.jpg`} className="container-imagen-completa pl-5 "
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ConservacionAreasProtegidas;