import React, { useEffect, useState } from "react";
import useIsMobile from "../hooks/useIsMobile";
import { useTrackedState } from '../Store';

const DownloadButton = (props) => {
    const Texto = () => {
        const state = useTrackedState();
        const { language } = state;

        return (
            <>
                {language === 'es' &&
                    <>
                        <p className="ml-md-5 ml-lg-0">Descargue acá <br /> estos datos</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <p className="pt-2 ml-md-5 ml-lg-0">Download data</p>
                    </>
                }
            </>
        )
    }

    const { clase, linkDescarga, tieneMargin, position } = props;
    const [positionBoton, setPositionBoton] = useState(position);
    const [bottom, setBottom] = useState();
    const [marginTop, setMarginTop] = useState();

    const isMobile = useIsMobile();

    useEffect(() => {
        var espacioInferior = document.getElementsByClassName('espacio-inferior')[0].getBoundingClientRect().height;

        setPositionBoton(position)
        setBottom(espacioInferior)
    }, [position])

    useEffect(() => {
        if (tieneMargin) {
            var espacioInferior = document.getElementsByClassName('espacio-inferior')[0].getBoundingClientRect().height;
            setMarginTop(-espacioInferior)
        }
    }, [isMobile])


    return (
        <div
            className={`contenedor-btn-descarga d-flex justify-content-end w-100`}
            style={{
                bottom: position !== undefined ? bottom : 0,
                marginTop: marginTop,
                position: positionBoton !== undefined ? positionBoton : "relative"
            }}
        >
            <a href={linkDescarga} target="_blank"
                className={`btn-descarga mt-5 mt-lg-0 col-9 col-sm-5 col-md-4 col-lg-auto  ${clase}`}
                style={{ backgroundImage: `url(./img/descarga.svg)` }} >
                <Texto />
            </a>
        </div>
    )

}

export default DownloadButton;
