
import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';

const ArbustalesCarbonales = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;

  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>
            <p className="mt-lg-3">Arbustales densos mayormente siempreverdes (<em>Thevetia</em> sp., <em>Libidibia coriaria</em>),  y con espinas (<em>Prosopis</em> sp., <em>Cercidium</em> sp.), con ocasionales árboles (<em>Cavanillesia chicamochae</em>), y presencia importante de cactáceas en todos los estratos, conspicuamente columnares(<em>Acanthocereus</em> sp.) y foliadas  (<em>Opuntia</em> sp.).Bromelias, cactáceas y gramíneas rastreras. En las cañadas, árboles de porte medio a alto (<em>Pithecellobium dulce</em>, <em>Anacardium giganteum</em>). Topografía montañosa, pendientes fuertes.</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}
                >Energía
                </li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_2.png'})`, }}>Infraestructura vial</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Uso del suelo</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Riesgo de incendios</li>
                <li className="principales-amenazas-remocion pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_remocion_1.png'})`, }}>Remoción en masa</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/arbustales_carbonales/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>
            <p className="mt-lg-3">Dense shrubs mostly evergreen (<em>Thevetia</em> sp., <em>Libidibia coriaria</em>), and often with thorns (<em>Prosopis</em> sp., <em>Cercidium</em> sp.), with occasional trees (<em>Cavanillesia chicamochae</em>), with cacti in all strata, conspicuously columnar (<em>Acanthocereus</em> sp.) and foliated (<em>Opuntia</em> sp.). Ground cover of bromeliads, cacti and crawling grasses. In the canyons, medium to high trees (<em>Pithecellobium dulce</em>, <em>Anacardium giganteum</em>). Mountainous topography with steep slopes.</p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}>Energy</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_2.png'})`, }}>Road infrastructure</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
                <li className="principales-amenazas-remocion pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_remocion_1.png'})`, }}>Massive removal</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/arbustales_carbonales/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }

  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5">
        <div className="col-lg-4 pl-5 pt-5">
        <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5">Arbustales y cardonales de valles intraandinos</h3>
              </>
            }
            {language === 'en' &&
              <>
                 <h3 className="mt-0 mt-lg-5">Dry shrublands of the Andean Valleys</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
              <img src={`./img/estado_actual/arbustales_carbonales/info_arbustales_carbonales_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            
            <div className="col-lg-9 px-0 mapa-arbustales-y-cardonales">
                <MapaContainer
                  img="./img/estado_actual/arbustales_carbonales/Mapas08.jpg"
                />
            </div>
            
          </div>
        </div>
      </div>
    </>
  )
}

export default ArbustalesCarbonales;