import React from 'react';
import {Element} from 'react-scroll';


//Componentes
import FadeElement from '../../components/FadeElement';
import MenuLateral from '../../components/MenuLateral';
import normalizeString from '../../js/utilities/normalizeString';
import { useTrackedState } from '../../Store';
import PerdidaEcosistemas from './PerdidaEcosistemas';
import PerdidaEcosistemasActividadHumana from './PerdidaEcosistemasActividadHumana';

const PerdidaEcosistemas_App = () => {
  const state = useTrackedState();
  const { language } = state;
  
  const secciones = {
    es: ['Pérdida de ecosistemas', 'Proceso histórico de pérdida de ecosistemas'],
    en: ['Loss of Ecosystems', 'Historic process of ecosystem loss']
  }

  return(
    <>
      <MenuLateral 
        section={'perdida-ecosistemas'}
        subsections={secciones}
      />
      <Element name={`perdida-ecosistemas-${normalizeString(secciones[language][0])}`}>
        <PerdidaEcosistemas/>
      </Element>
      <Element name={`perdida-ecosistemas-${normalizeString(secciones[language][1])}`}>
        <PerdidaEcosistemasActividadHumana/>
      </Element>
      <FadeElement/>
    </>
  )
}

export default PerdidaEcosistemas_App;