import React from 'react';
import {Element} from 'react-scroll';


//Componentes
import FadeElement from '../../components/FadeElement';
import MenuLateral from '../../components/MenuLateral';
import PerdidaProcesosEcologicos from './PerdidaProcesosEcologicos';
import ProcesosEcologicos from './ProcesosEcologicos';
import EspacioInferior from '../../components/EspacioInferior';

import { useTrackedState } from '../../Store';
import normalizeString from '../../js/utilities/normalizeString';


const ProcesosEcologicos_App = () => {
  
  const state = useTrackedState();
  const { language } = state;
  
  const secciones = {
    es: ['Procesos ecológicos', 'Pérdida de procesos ecológicos'],
    en: ['Ecological Processes', 'Loss of Ecological Processes']
  }

  return(
    <>
      <MenuLateral 
        section={'procesos-ecologicos'}
        subsections={secciones}
      />
      <Element name={`procesos-ecologicos-${normalizeString(secciones[language][0])}`}>
        <ProcesosEcologicos/>
      </Element>
      <Element name={`procesos-ecologicos-${normalizeString(secciones[language][1])}`}>
        <PerdidaProcesosEcologicos/>
      </Element>
      <FadeElement/>
      <EspacioInferior/>
    </>
  )
}

export default ProcesosEcologicos_App;