import React from "react";
import { useTrackedState } from '../../../Store';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';

const PortadaBosquesSecosVallesIntraandinos = () => {
    const state = useTrackedState();
    const {language} = state;
    const [heightContainer, containerRef] = useGeneralHeightContainer();
    return(
      <div 
        ref={containerRef}
        style={{height: `${heightContainer}px`}}
        className="height-responsive"
      >
        <div className="container d-flex justify-content-center h-100 height-responsive pt-5 pt-lg-0 pl-5 pl-lg-0">
          <div className="text-center">
            <img 
              src='img/estado_actual/bosques_secos_valles/portada2.jpg' 
              alt="imagen de Bosques secos tropicales del Caribe" 
              className="img-portada"
            />
            {language === 'es' &&
              <h3 >Bosques secos <br/>de valles intraandinos</h3>
            }
            {language === 'en' &&
              <h3 >Dry Forests of the <br/>Andean Internal Valleys</h3>
            }
          </div>
        </div>
      </div>
    )
  }

  export default PortadaBosquesSecosVallesIntraandinos;