import React, {useCallback} from 'react';
const EspacioInferior = () =>{
  const crearEspacioInferior = useCallback(node=>{
    const crearEspacio =() => {
      if(node!== null){
        //añadir este id al menu
        let menuHeight = document.getElementById('menu').getBoundingClientRect().height;
        node.style.height = `${menuHeight}px`;
      }
    }
    crearEspacio();
    window.addEventListener('resize', crearEspacio)
    return ()=>{
      window.removeEventListener('resize', crearEspacio)
    }
  }, [])
  return(
    <div className="espacio-inferior" ref={crearEspacioInferior}></div>
  ) 
}
export default EspacioInferior;