import React from 'react';
import {useLocation} from 'react-router-dom';

import { useHeightMenu} from '../hooks/useHeightMenu';



const FadeElement = () => {
  const pathnames = ['/', '/introduccion']
  const location = useLocation();
  const {pathname} = location;
  const [heightMenu, containerRef] = useHeightMenu();

  return(
    
    <div className="fade-element" id="fade-element"
      ref={containerRef} 
      style={{
        opacity: pathnames.includes(pathname) ? '0' : 1,
        bottom: `${heightMenu}px`}}
    />
  )
}
export default FadeElement;