import React, { useEffect } from 'react';
//Componentes
import EstadoEcosistemas from './EstadoEcosistemas';
import Introduccion from './Introduccion';
import Presentacion from './Presentacion';
import TipoEcosistemas from './TipoEcosistemas';
import Portada from './Portada';
import { useLocation } from 'react-router-dom';
import ScrollAIntroduccion from './ScrollAIntroduccion';
import { Element } from 'react-scroll';
import MenuLateral from '../../components/MenuLateral';
import FadeElement from '../../components/FadeElement';
import EspacioInferior from '../../components/EspacioInferior';

import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { useTrackedState } from '../../Store';
import normalizeString from '../../js/utilities/normalizeString';

const IntroduccionSection = () => {
  const location = useLocation();
  const { pathname } = location;

  const state = useTrackedState();
  const { language } = state;

  
  const secciones = {
    es: ['Presentación', 'Introducción', 'Estado de los ecosistemas', 'Tipo de ecosistemas en Colombia'],
    en: ['Presentation', 'Introduction', "General Ecosystem Risk", "Types of Ecosystems"]
  }


  useEffect(() => {
    //ReactTooltip.rebuild(); // el rebuild se ejecuta pero no cambia nada.
    if (pathname === '/introduccion') {
      ScrollAIntroduccion();
    }
  }, [pathname])

  useEffect(() => {
    window.addEventListener("resize", ScrollTrigger.refresh)
  }, [])

  return (
    <>
      <MenuLateral
        section={'introduccion'}
        subsections={secciones}
      />
      <Portada />
      <Element name={`introduccion-${normalizeString(secciones[language][0])}`}>
        <Presentacion />
      </Element>
      <Element name={`introduccion-${normalizeString(secciones[language][1])}`}>
        <Introduccion />
      </Element>
      <Element name={`introduccion-${normalizeString(secciones[language][2])}`}>
        <EstadoEcosistemas />
      </Element>
      <Element name={`introduccion-${normalizeString(secciones[language][3])}`}>
        <TipoEcosistemas />
      </Element>
      <FadeElement />
      <EspacioInferior />
    </>
  )
}

export default IntroduccionSection;