import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';


const ArbustalesSecos = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;
  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>
            <p className="mt-lg-3">Arbustales bajos densos mayormente siempreverdes (<em>Dodonaea viscosa</em>, <em>Myrsine</em> sp., <em>Condalia thomasiana</em>) y con espinas (<em>Caesalpinia spinosa</em>, <em>Lycianthes lycioides</em>, <em>Cestrum densiflorum</em>), y presencia importante de cactáceas foliadas (<em>Opuntia</em> sp.), <em>Melocactus</em> sp., <em>Agave</em> sp., Crassulaceae, Bromeliaceae y gramíneas rastreras. En las cañadas, árboles y arbustos de porte medio (<em>Rapanea guianensis</em>, <em>Duranta</em> sp.). Topografía colinada. Suelos poco profundos con <em>claypan</em>, ligermente ácidos y muy suceptibles a la erosión.</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}
                >Energía</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_2.png'})`, }}
                >Infraestructura vial</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Uso del suelo</li>
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_1.png'})`, }}
                >Minería</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}
                >Riesgo de incendios</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/arbustales_secos/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>
            <p className="mt-lg-3">Low dense mostly evergreen shrublands, with shrubs and small trees (<em>Dodonaea viscosa</em>, <em>Myrsine</em> sp., <em>Condalia thomasiana</em>) thorny shrubs (<em>Caesalpinia spinosa</em>, <em>Lycianthes lycioides</em>, <em>Cestrum densiflorum</em>), and conspicuous foliar cactuses (<em>Opuntia</em> sp.), <em>Melocactus</em> sp., <em>Agave</em> sp., Crassulaceae, Bromeliaceae, and crawling grasses. In the ravines trees and shrubs of medium size (<em>Rapanea guianensis</em>, <em>Duranta</em> sp.). Hilly topography. Shallow soils with <em>claypan</em>, slightly acidic and very susceptible to erosion.</p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_3.png'})`, }}>Energy</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_2.png'})`, }}>Road infrastructure</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-mineria pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_mineria_1.png'})`, }}>Mining</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/arbustales_secos/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }
  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5">
        <div className="col-lg-4 pl-5 pt-5">
          <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5 ">Arbustales secos altoandinos de la cordillera Oriental</h3>
              </>
            }
            {language === 'en' &&
              <>
                 <h3 className="mt-0 mt-lg-5 ">Dry Shrublands of the Eastern Cordillera Highlands</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-absolute h-100">
              <img src={`./img/estado_actual/arbustales_secos/info_arbustales_secos_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            <div className="col-lg-9 px-0">
              <MapaContainer
                img="./img/estado_actual/arbustales_secos/Mapas09.jpg"
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default ArbustalesSecos;