const calcularPropiedadesScrollTrigger = (refContenido, claseContenido, refImagenes, claseImagenes, numImagenesScroll, headerSpace) => {
    //Valores de la interfaz
    var altoMenu = document.getElementById('menu').getBoundingClientRect().height;
    var espacioInferior = document.getElementsByClassName('espacio-inferior')[0].getBoundingClientRect().height * 3;
    var difuminado = document.getElementById("fade-element");    

    //Valores del contenido
    var altoContenedorContenido = refContenido.current.offsetHeight;
    var altoContenedorImagenes = refImagenes.current.offsetHeight - espacioInferior;
    var altoImagen = altoContenedorImagenes / numImagenesScroll;
    
    var espacioDisponible=window.innerHeight - altoMenu - (headerSpace + headerSpace / 2);    
    var espacioStart = (window.innerHeight - altoImagen);



    //Mira si el contenedor del contenido es mayor al contenedor de las imágenes
    var condicional = altoContenedorContenido > altoContenedorImagenes;
    //Mira si el contenedor del contenido es mas alto que las imágenes
    var condicional2 = altoContenedorContenido > espacioDisponible;


    //Porpiedades ScrollTrigger
    var trigger = condicional
        ? claseContenido
        : claseImagenes

    var pin = condicional
        ? false
        : claseContenido

    var start;
    if (!condicional) {
        var numStart = ((altoContenedorContenido - altoImagen) + altoMenu ) - espacioStart;
        start = condicional2
            ? `top -=${numStart}px`
            : `top ${headerSpace + headerSpace / 2}px`;
    }
    
    if(start === undefined){
        start = `top ${headerSpace + headerSpace / 2}px`
    }
        
    return { trigger, pin, start };
}

export default calcularPropiedadesScrollTrigger;
