import React, { useRef, useState, useEffect } from 'react';
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import { useHeightContainer } from '../hooks/useHeightContainer';

const MapaContainer = (props) => {
  const { img, className, centrar } = props;
  const image = useRef(null);
  const [imageHover, setImageHover] = useState(false);
  const [heightContainer, containerRef, headerSpace] = useHeightContainer();
  const [valorTopBotones, setValorTopBotones] = useState();

  const [marginTop, setMarginTop] = useState();
  const refMapa = useRef()

  useEffect(() => {
    if (centrar) {
      centrarMapa();

      window.addEventListener("resize", centrarMapa);
      return () => {
        window.removeEventListener("resize", centrarMapa)
      }
    }

    topBotones()
    window.addEventListener("resize", topBotones);

    return () => {
      window.removeEventListener("resize", topBotones)
    }
  }, [])

  
  const centrarMapa = () => {
    var mapa = image.current;
    var espacioStart = (window.innerHeight - mapa.offsetHeight) / 2;
    setMarginTop(espacioStart);
  }


  const topBotones = () => {
    if (image.current !== undefined) {
      var heightImg = image.current.getBoundingClientRect().height;
      var top = window.innerHeight - heightImg;
    }
  }

  return (
    <div className="mapa-component-container h-100"
      onMouseEnter={() => {
        setImageHover(true)
      }}
    >
      <div className={`mapa-component-wrapper mr-0 h-100 ${className ? className : ''}`}>
        <TransformWrapper
          defaultScale={1}
          minScale={1}
          defaultPositionX={0}
          defaultPositionY={0}
          pan={{
            paddingSize: 0,
            padding: true
          }}
          options={{
            limitToWrapper: true,
          }}
          wheel={{
            disabled: true,
            limitsOnWheel: true,
          }}
          onZoomChange={(cosa) => {
            console.log(cosa)
            // cosa.positionY = 0
          }}

        >

          {({ setPositionY, zoomIn, zoomOut, resetTransform, scale, ...rest }) => (
            <>
              <div
                className="mapa-container-botones px-3"
                style={{
                  top: headerSpace
                }}
              >

                <button
                  className="boton-zoom-in" style={{ backgroundImage: `url(${'./img/zoom-in.png'})` }} onClick={zoomIn}>
                  Acercar
                </button>

                <button className="boton-zoom-out"
                  style={{ backgroundImage: `url(${'./img/zoom-out.png'})` }}
                  onClick={zoomOut}>
                  Alejar
                </button>

                <button className="boton-back"
                  style={{
                    backgroundImage: `url(${'./img/re-size.png'})`,
                    opacity: scale > 1 ? 1 : 0,
                    visibility: scale > 1 ? 'visible' : 'hidden'
                  }}
                  onClick={resetTransform}>Volver al inicio
                </button>
              </div>
              <div
                style={{
                  marginTop: marginTop
                }}
              >
                <TransformComponent>
                  <div className="mapas-component-mapa position-relative w-100" ref={image} >
                    <div className="mapa-component-gif"
                      style={
                        {
                          backgroundImage: `url('./img/Lupa.svg')`,
                          opacity: imageHover ? 0 : 1,
                          visibility: imageHover ? 'hidden' : 'visible'
                        }}
                    />
                    <img
                      ref={image}
                      src={`${img}`} alt="imagen de ambiente 1" className="img-fluid"
                      onLoad={()=>centrarMapa()}
                    />
                  </div>
                </TransformComponent>

              </div>
            </>
          )}
        </TransformWrapper>

      </div>
    </div>

  )
}


export default MapaContainer;