import gsap from 'gsap'
import { ScrollToPlugin } from 'gsap/all'
gsap.registerPlugin(ScrollToPlugin)
const ScrollAIntroduccion = () => {
  let tl = gsap.timeline();
  tl.to(window, {duration: 1, scrollTo:  {y: '#presentacion', offsetY: 50}})
  .to('#main-header', {top: 0, duration: 0.5})
}

export default ScrollAIntroduccion;