import React from 'react';
import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import DownloadButton from '../../components/Btn-descarga';
const RiesgoCriterio = () => {
    const [heightContainer, containerRef] = useHeightContainer();
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                {language === 'es' &&
                    <>
                        <div className="title-fixed"
                            style={{
                                top: `50px`
                            }}
                        >
                            <h2>Riesgo por criterio para ecosistemas en estado crítico</h2>
                        </div>


                    </>
                }
                {language === 'en' &&
                    <>
                        <div className="title-fixed"
                            style={{
                                top: `50px`
                            }}
                        >
                            <h2>Evaluation Criteria of High Risk Ecosystems</h2>
                        </div>
                    </>
                }
            </>
        )
    }

    const state = useTrackedState();
    const { language } = state;

    return (
        <div id="riesgo-por-criterio-para-ecosistemas-en-estado-critico">
            <div className="container-fluid row mx-0 justify-content-end py-5 align-items-start px-0 position-relative">
                <div className="col-lg-6 pl-5 d-flex justify-content-center px-5">
                    <div className="introduccion-textos">
                        <Textos />
                        <img src={`./img/perdida_ecosistema/brosimum.jpg`} alt="imagen de brosimum" className="col-6 img-fluid" />
                        {language === 'es' &&
                            <>
                                <ul className="list-style">
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/cr.png'})`, }}></span>Estado Crítico</li>
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/en.png'})`, }}></span>Peligro</li>
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/vu.png'})`, }}></span>Vulnerable</li>
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/lc.png'})`, }}></span>Sin preocupación</li>
                                </ul>

                            </>
                        }
                        {language === 'en' &&
                            <>
                                <ul className="list-style">
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/cr.png'})`, }}></span>Critical</li>
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/en.png'})`, }}></span>Endangered</li>
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/vu.png'})`, }}></span>Vulnerable</li>
                                    <li><span style={{ backgroundImage: `url(${'./img/perdida_ecosistema/lc.png'})`, }}></span>Least concern</li>
                                </ul>

                            </>}
                    </div>
                </div>
                <div className="col-lg-8  px-0 ml-5 ml-lg-0 sticky">

                    <div
                        className="d-block d-lg-flex flex-lg-column align-items-center justify-content-center height-responsive"
                    >
                        <img src={`./img/perdida_ecosistema/riesgo_critico_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
                    </div>

                    <DownloadButton
                        linkDescarga="https://drive.google.com/drive/folders/1elNip3dA8YQoejqeAs2u07XQKd52NJ3U?usp=sharing"
                    />
                </div>
            </div>
        </div>
    )
}

export default RiesgoCriterio;
