import React from 'react';
import MapaContainer from '../../../components/MapaContainer';
import { useGeneralHeightContainer } from '../../../hooks/useGeneralHeightContainer';
import { useHeightContainer } from '../../../hooks/useHeightContainer';
import { useTrackedState } from '../../../Store';
import '../../../scss/sections/estado_actual.scss';

const BosquesSecos = () => {
  const [heightContainer, containerRef] = useGeneralHeightContainer();
  const [headerSpace] = useHeightContainer();
  const state = useTrackedState();
  const { language } = state;
  const Textos = () => {
    return (
      <>
        {language === 'es' &&
          <>
             <p className="mt-lg-3">Bosque de hasta 20 m con dosel irregular de árboles caducifolios (<em>Tabebuia</em> spp., <em>Bombax</em> sp., <em>Bursera simaruba</em>, <em>Capparis</em> sp., <em>Trichilia</em> sp., <em>Astronium graveolens</em>, <em>Hura crepitans</em>, <em>Bulnesia carrapo</em>). Estratificación poco definida con abundantes lianas y con un sotobosque arbustivo. Palmas (<em>Attalea butyracea</em>, <em>Sabal muaritiiformis</em>) conspicuas que llegan al dosel. Drenajes con corredores de árboles siempreverdes (<em>Anacardium giganteum</em>).</p>
            <div>
              <p><strong>Principales amenazas</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-hidrocarburos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_hidrocarburos_3.png'})`, }}
                >Hidrocarburos</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}
                >Uso del suelo</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }} >Energía</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_1.png'})`, }}>Infraestructura vial</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Riesgo de incendios</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_secos/info.jpg' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>
        }
        {language === 'en' &&
          <>
            
            <p className="mt-lg-3"><p>Forest up to 20 m with irregular canopy of deciduous trees (<em>Tabebuia</em> spp., <em>Bombax</em> sp., <em>Bursera simaruba</em>, <em>Capparis</em> sp., <em>Trichilia</em> sp., <em>Astronium graveolens</em>, <em>Hura crepitans</em>, <em>Bulnesia carrapo</em>). Poorly defined stratification with abundant lianas, and a dense shrubby undergrowth with some vines. Conspicuous palms (<em>Attalea butyracea</em>, <em>Sabal muaritiiformis</em>) that reach the canopy. Rivers/creeks with corridors of evergreen trees (<em>Anacardium giganteum</em>).</p></p>
            <div>
              <p><strong>Main Threats</strong> </p>
              <ul className="principales-amenazas">
                <li className="principales-amenazas-hidrocarburos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_hidrocarburos_3.png'})`, }}>Fossil fuels extraction</li>
                <li className="principales-amenazas-suelos pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_suelos_2.png'})`, }}>Agriculture</li>
                <li className="principales-amenazas-energia pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_energia_2.png'})`, }}>Energy</li>
                <li className="principales-amenazas-infraestructura pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_infraestructura_1.png'})`, }}>Road infrastructure</li>
                <li className="principales-amenazas-incendios pb-3" style={{ backgroundImage: `url(${'./img/estado_actual/iconos_estado_actual/icono_incendios.png'})`, }}>Fire risk</li>
              </ul>
            </div>
            <div className="mb-lg-5">
              <img src='./img/estado_actual/bosques_secos/info_en.JPG' alt="imagen de ambiente 1" className="img-fluid" />
            </div>
          </>}
      </>
    )
  }
  return (
    <>
      <div className="row mx-auto py-5 align-items-start pl-lg-5 mb-5">
        <div className="col-lg-4 pl-5 pt-5">
        <div className="title-sticky"
            style={{
              top: `${headerSpace+40}px`
            }}
          >
            {language === 'es' &&
              <>
                <h3 className="mt-0 mt-lg-5">Bosques secos <br/>tropicales del Caribe</h3>
           
              </>
            }
            {language === 'en' &&
              <>
                <h3 className="mt-0 mt-lg-5">Caribbean Tropical <br/>Dry Forests</h3>
              </>
            }

          </div>
          <Textos />
        </div>
        <div className="col-lg-10 pr-0"
          style={{
            position: 'sticky',
            top: 0
          }}
        >
          <div
            ref={containerRef}
            style={{ height: `${heightContainer}px` }}
            className="row mx-0 height-responsive"
          >
            <div className="col-lg-5 d-flex justify-content-center align-items-center position-lg-labsolute h-100">
              <img src={`img/estado_actual/bosques_secos/info_bosques_secos_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
            </div>
            <div className="col-lg-9 px-0 mapa-bosques-secos">
              <MapaContainer
                img="./img/estado_actual/bosques_secos/Mapas03.jpg"
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default BosquesSecos;