import React from 'react';
import BosquesHumedos from './BosquesHumedos/';
import MenuLateral from '../../components/MenuLateral';
import FadeElement from '../../components/FadeElement';
import {Element} from 'react-scroll';
import BosquesSubhumedosApp from './BosquesSubhumedos';
import BosquesSecosApp from './BosquesSecos';
import SabanasPiedemonteApp from './SabanasPiedemonte';
import BosquesHumedosTropicalesApp from './BosquesHumedosTropicales';
import BosquesSubhumedosAltiplanoApp from './BosquesSubhumedosAltiplano';
import BosquesSecosVallesIntraandinosApp from './BosquesSecosVallesIntraandinos';
import ArbustalesCarbonalesApp from './ArbustalesCarbonales';
import ArbustalesSecosApp from './ArbustalesSecos';
import HumedalesAltiplanoApp from './HumedalesAltiplano';
import EstadoActualEcosistemas from './EstadoActualEcosistemas';
import Amenazas from './Amenazas';
import AmenazasSectores from './AmenazasSectores';
import EspacioInferior from '../../components/EspacioInferior';
import { useTrackedState } from '../../Store';
import normalizeString from '../../js/utilities/normalizeString';

const EstadoActual = () => {

  const state = useTrackedState();
  const { language } = state;

  
  const secciones = {
    es: ['Estado actual de ecosistemas en riesgo', 'Amenazas', 'Amenazas y sectores productivos', 'Bosques húmedos tropicales del piedemonte llanero', 'Bosques subhúmedos tropicales del Caribe-Andes', 'Bosques secos tropicales del Caribe', 'Sabanas del piedemonte llanero', 'Bosques subhúmedos tropicales altoandinos', 'Bosques subhúmedos del altiplano cundiboyacense', 'Bosques secos de valles intraandinos', 'Arbustales y cardonales de valles intraandinos', 'Arbustales secos altoandinos de la cordillera Oriental', 'Humedales del altiplano de la cordillera Oriental'],
    en: ['Current Condition of Ecosystems under High Risk', 'Threats', 'Threatening Productive Sectors', 'Tropical Humid Forests in the Llanos piedmont', 'Caribbean-Andean Tropical Sub-humid Forests', 'Caribbean Tropical Dry Forests', 'Savannas of the Llanos Piedmont', 'Sub-Humid Tropical Forests of the Andean Highlands', 'Sub-Humid Forests of the Cundinamarca-Boyacá High-plain', 'Dry Forests of the Andean Internal Valleys', 'Dry shrublands of the Andean Valleys', 'Dry Shrublands of the Eastern Cordillera Highlands', 'Wetlands of the Eastern Cordillera high-plain']
  }

  return(
    <>
      <MenuLateral 
        section={'estadoactual'}
        subsections={secciones}
      />
      <Element name={`estadoactual-${normalizeString(secciones[language][0])}`}>
        <EstadoActualEcosistemas/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][1])}`}>
        <Amenazas/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][2])}`}>
        <AmenazasSectores/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][3])}`}>
        <BosquesHumedos/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][4])}`}>
        <BosquesSubhumedosApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][5])}`}>
        <BosquesSecosApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][6])}`}>
        <SabanasPiedemonteApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][7])}`}>
        <BosquesHumedosTropicalesApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][8])}`}>
        <BosquesSubhumedosAltiplanoApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][9])}`}>
        <BosquesSecosVallesIntraandinosApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][10])}`}>
        <ArbustalesCarbonalesApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][11])}`}>
        <ArbustalesSecosApp/>
      </Element>
      <Element name={`estadoactual-${normalizeString(secciones[language][12])}`}>
        <HumedalesAltiplanoApp/>
      </Element>

      <FadeElement/>
    </>
  )
}

export default EstadoActual;