import React from 'react';
import { Element } from 'react-scroll';

import { gsap } from 'gsap/gsap-core';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

//Componentes
import FadeElement from '../../components/FadeElement';
import MenuLateral from '../../components/MenuLateral';
import CriteriosEvaluacion from './CriteriosEvaluacion';
import RiesgoCriterio from './RiesgoCriterio';
import { useTrackedState } from '../../Store';
import normalizeString from '../../js/utilities/normalizeString';
import EspacioInferior from '../../components/EspacioInferior';

gsap.registerPlugin(ScrollTrigger);
gsap.core.globals('ScrollTrigger', ScrollTrigger);
const EvaluacionRiesgo = () => {

    const state = useTrackedState();
    const { language } = state;
    
    const secciones = {
      es: ['Criterios de evaluación y categorías de riesgo', 'Riesgo por criterio para ecosistemas en estado crítico'],
      en: ['Evaluation Criteria and Risk Categories', 'Evaluation Criteria of High Risk Ecosystems']
    }

    return (
        <>
            <MenuLateral
                section={'evaluacion-riesgo'}
                subsections={secciones}
            />
            <Element name={`evaluacion-riesgo-${normalizeString(secciones[language][0])}`}>
                <CriteriosEvaluacion />
            </Element>
            <Element name={`evaluacion-riesgo-${normalizeString(secciones[language][1])}`}>
                <RiesgoCriterio />
            </Element>
            <EspacioInferior/>
            <FadeElement />
        </>
    )
}

export default EvaluacionRiesgo;