import React, {useEffect, useRef } from 'react';
import gsap from 'gsap';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import useIsMobile from '../../hooks/useIsMobile';


let enterToScroll = 0;
const Presentacion = () => {
  const Textos = () => {
    const state = useTrackedState();
    const { language } = state;
    return (
      <>
        {language === 'es' &&
          <>
            <div className="mt-lg-3">
              <p>Los ecosistemas son conjuntos de plantas, animales y microbios que viven en entornos y lugares particulares. Incluyen toda la variedad de bosques, sabanas, desiertos, montañas, pantanos, lagos y arroyos, así como manglares, playas, arrecifes y océanos profundos. Los humanos somos parte integral de este mundo natural. Influimos en la vegetación que cubre la superficie, el agua que fluye a través de ella, los animales que la habitan, el ciclo de nutrientes, el carbono e incluso el clima global.</p>

              <p>Para las personas, los ecosistemas también representan un profundo sentido de lugar. Dan forma a nuestra identidad, nuestra cultura y nuestras creencias. Garantizan nuestra existencia al proporcionar los elementos esenciales de la vida: agua limpia y aire; alimentos, fibra y otras materias primas, entre otros beneficios, como reducir nuestra exposición a desastres naturales. Aunque los ecosistemas son cruciales para el ser humano, el rápido crecimiento de nuestra población y el uso de nuevas tecnologías permiten una explotación cada vez más intensiva de los recursos naturales, y así amenazan con el colapso de algunos de nuestros ecosistemas. Si bien los ecosistemas colapsados pueden retener algunos elementos de sus predecesores, las consecuencias de este cambio transformador implican grandes pérdidas de identidad, biodiversidad y función, exponiendo a las personas a la privación cultural, la pérdida de medios de vida y la disminución de la salud y el bienestar. </p>

              <p>La nueva disciplina de la evaluación del riesgo de los ecosistemas ofrece oportunidades para planificar y gestionar de manera proactiva los ecosistemas para mantener sus valores críticos. Al conocer cuáles ecosistemas tienen mayor riesgo de colapso, podemos planificar e implementar estrategias para evitar pérdidas. Las estrategias deben ser tanto preventivas como restaurativas, para garantizar la protección de los mejores remanentes de cada tipo de ecosistema e identificar las mejores oportunidades para revertir las disminuciones en los más degradados.</p>

              <p>Colombia es uno de los países megadiversos del mundo, con una gran riqueza de vida vegetal y animal en una gran diversidad de ecosistemas tropicales, que son vitales para el bienestar de las personas dentro y más allá de las fronteras de Colombia. En esta etapa de posconflicto, los colombianos están emergiendo rápidamente como líderes mundiales en la conservación de la naturaleza. Esta primera Lista Roja de Ecosistemas colombianos es una demostración enfática de ese hecho. Con datos exhaustivos y rigurosos sobre el estado de 81 ecosistemas terrestres en todo el país, sienta las bases para mejorar la comprensión de las funciones vitales de los ecosistemas y cómo se pueden mitigar las amenazas a las que estos están sujetos.</p>

              <p id="cambio-imagen-presentacion2">Los planes para mitigar los riesgos de los ecosistemas colombianos deben involucrar tanto al Gobierno, como a la industria y la comunidad. La Lista Roja de Ecosistemas (LRE) muestra dónde y cómo deben enfocarse los esfuerzos mediante el fortalecimiento de la red de áreas protegidas, la restauración de ecosistemas degradados, la movilización y el empoderamiento de la población local, y la regulación del desarrollo de la industria y la infraestructura.</p>

              <p>Esta estrategia puede lograr beneficios económicos evitando, minimizando y, cuando corresponda, compensando los impactos ambientales y, por lo tanto, manteniendo el capital natural, que los colombianos valoran tanto.</p>

              <p>Esta excelente publicación establece un alto estándar para que otros la sigan. Combina ciencia sólida con gráficos y mapas hábiles para comunicar mensajes poderosos sobre los valores de los ecosistemas de Colombia, las amenazas a su sostenibilidad y el camino a la acción para garantizar su futuro.</p>

              <p>
                <strong>Profesor David Keith</strong><br />
                <em>Líder temático global</em><br />
                <em>Comisión de Manejo de Ecosistemas, UICN</em>
              </p>
            </div>
          </>
        }
        {language === 'en' &&
          <>
            <div className="mt-lg-3">
              <p>Ecosystems are the assemblages of plants, animals and microbes that live in particular environments in particular places. They include the full variety of forests, savannas, deserts, mountains, marshes, lakes and streams, as well as mangroves, beaches, reefs and the deep oceans. People are integral parts of this natural world. We influence the vegetation that covers the surface, the water that flows through it, the animals that inhabit it, the cycling of nutrients, and the carbon. And even the global climate.</p>

              <p>For people, ecosystems also represent a deep sense of place. They shape our identity, our culture and our beliefs. They sustain our very existence by providing the essentials of life: Clean water and air; food, fiber and other raw materials and other benefits, such as reducing our exposure to natural disasters. Ecosystems are crucial to human-being, yet our rapidly growing population and use of new technologies enable ever-more intensive exploitation of natural resources that threatens some of our ecosystems with collapse. While collapsed ecosystems may retain some elements of their predecessors, the consequences of this transformational change involve major losses of identity, biodiversity and function, exposing people to cultural deprivation, loss of livelihoods and declines in health and well-being.</p>

              <p>The relatively new discipline of ecosystem risk assessment offers opportunities to proactively plan and manage ecosystems so that we may sustain their critical values. By understanding which ecosystems are most at risk of collapse, we can plan and implement strategies to avoid loss. The strategies must be both preventative and restorative, protecting the best remnant examples of each ecosystem type and identifying the best opportunities to reverse declines in the most extensively degraded ones.</p>

              <p>Colombia is one of the world’s megadiverse nations, with a vast wealth of plant and animal life within a great diversity of tropical ecosystems that are vital to the well-being of people within and far beyond Colombia’s borders. In the post-conflict era, Colombians are rapidly emerging as world leaders in nature conservation. This first Red List of Colombian Ecosystems is an emphatic demonstration of that fact. With comprehensive and rigorous data on the status of 81 terrestrial ecosystems across the country, it lays the foundations for national action to improve understanding of the vital roles of ecosystems and how the threats against them can be mitigated.</p>

              <p id="cambio-imagen-presentacion2">The blueprint for mitigating risks to Colombia’s ecosystems must involve government, industry and the community. The RLE shows where and how the efforts must be focused by strengthening the network of Protected Areas, restoring degraded ecosystems, mobilizing and empowering local people, and regulating development of industry and infrastructure.</p>

              <p>This is a strategy that can achieve economic benefits while avoiding, minimizing and, where appropriate, offsetting environmental impacts, and hence sustaining the natural capital that is so greatly valued by Colombians.</p>

              <p>This outstanding publication sets a high standard for others to follow. It combines sound science with skillful graphics and maps to communicate powerful messages about the values of Colombia’s ecosystems, the threats to their sustainability and the pathway to action for ensuring their future.</p>

              <p>
                <strong>Professor David Keith</strong><br />
                <em>Thematic Leader, IUCN RLE</em><br />
                <em>Commission for Ecosystem Management (CEM), IUCN</em>
              </p>
            </div>

          </>}
      </>
    )
  }


  const presentacion = useRef(null)

  useEffect(()=>{
    const scrollPresentacion = () => {
      let node = presentacion.current;
      if (node !== null) {
        let htmlScroll = document.getElementsByTagName('html')[0].scrollTop;
        let presentacionScroll = node.getBoundingClientRect().top;
        if (htmlScroll >= (presentacionScroll - 100)) {
          enterToScroll += 1;
        } else {
          enterToScroll = 0;
          gsap.to('#main-header', { duration: 0.5, top: -70 })
          gsap.to('#menu-lateral', { duration: 0.5, left: -70 })
          gsap.to('#fade-element', { duration: 0.5, opacity: 0 })
          changeActiveItem(true)
        }
      }
      if (enterToScroll === 1) {
        gsap.to('#main-header', { duration: 0.5, top: 0 })
        gsap.to('#menu-lateral', { duration: 0.5, left: 0 })
        gsap.to('#fade-element', { duration: 0.5, opacity: 0 })
        gsap.to('#fade-element', { duration: 0.5, opacity: 1 })
        changeActiveItem()
      }
    }
    window.addEventListener('scroll', scrollPresentacion)
    return () => {
      window.removeEventListener('scroll', scrollPresentacion)
    }

  }, [presentacion.current])


  const changeActiveItem = (remove) => {
    let nodes = document.querySelectorAll('#menu a');
    let className = 'menu-selected';
    let element = nodes[0];
    let includeClass = element.classList.contains(className);
    if(remove){
      if(includeClass){
        element.classList.remove(className)
      }
    }else{
      if(!includeClass){
        element.classList.add(className)
      }
    }
  }

  const state = useTrackedState();
  const { language } = state;
  const isMobile = useIsMobile();
  const [heightContainer, containerRef, headerSpace] = useHeightContainer();


  return (
    <div id="presentacion" ref={presentacion}>
      <div className="container-fluid row mx-0 justify-content-end justify-content-lg-between flex-lg-nowrap py-5 align-items-start px-0">

        <div className="col-lg-6 pl-5 d-flex justify-content-center px-5">
          <div className="introduccion-textos">
            
            <div className="title-sticky"
              style={{
                top: `${headerSpace}px`
              }}
            >
              {language === 'es' &&
                <>
                  <h2>Presentación</h2>
                </>
              }
              {language === 'en' &&
                <>
                  <h2>Presentation</h2>
                </>
              }

            </div>
            <div className="introduccion-textos presentacion">
              <Textos />
            </div>
          </div>
        </div>



        <div
          className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-presentacion m-0 mt-5"
          style={{
            position: "sticky",
            top: heightContainer/2
          }}

        >
          <div
            ref={containerRef}
            style={{
              height: isMobile ? "auto" : `${heightContainer}px`
            }}
          >
            <img src={`./img/introduccion/ambiente1.jpg`} alt="imagen de ambiente 1" className="img-fluid w-100" />
          </div>
        </div>
      </div>
    </div >


  )
}

export default Presentacion;