import { useState, useEffect, useRef } from 'react';

function useHeightMenu() {
  const [heightMenu, setHeightMenu] = useState(0);
  const containerRef = useRef(null);

  useEffect(()=>{
    const createHeight = () => {
      let node = containerRef.current;
      if(node!== null){
        let mainMenu = document.getElementsByClassName('main-menu')[0];
        let height =  mainMenu.getBoundingClientRect().height-20;
        setHeightMenu(height);
      }
    }
    createHeight();
    window.addEventListener('resize', createHeight)
    return() => {
      window.removeEventListener('resize', createHeight)
    }

  }, [containerRef.current])

  return [heightMenu, containerRef]
}

export { useHeightMenu};