import React, { useEffect, useRef } from 'react';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import calcularPropiedadesScrollTrigger from '../../js/utilities/calcularPropiedadesScrollTrigger';

import CambioImagenScroll from '../../components/CambioImagenScroll';
import DownloadButton from '../../components/Btn-descarga';

const PerdidaProcesosEcologicos = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                {language === 'es' &&
                    <>
                        <h2>Pérdida de procesos ecológicos</h2>
                        <p className="subtitulo pl-3">por cambio climático y actividad humana</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <h2>Loss of Ecological Processes</h2>
                        <p className="subtitulo pl-3">Due to the Climate Change and Human Actions</p>
                    </>}
            </>
        )
    }

    const state = useTrackedState();
    const { language } = state;
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();
    const refContenido = useRef();
    const refImagenes = useRef();
    const numImagenesScroll = 5;

    useEffect(() => {
        if (refContenido.current !== undefined) {
            setTimeout(() => {
                ScrollTrigger.create({
                    // markers: true,
                    pin: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-perdidad-procesos", refImagenes, ".imagenes-scrolll-perdidad-procesos", numImagenesScroll, headerSpace).pin,
                    pinSpacing: false,
                    start: () => cambiarEspacioStart(),
                    trigger: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-perdidad-procesos", refImagenes, ".imagenes-scrolll-perdidad-procesos", numImagenesScroll, headerSpace).trigger,
                    invalidateOnRefresh: true
                })
            }, 200);

        }
    }, [refContenido.current])


    const cambiarEspacioStart = () => {
        var alturaImagen = document.getElementsByClassName("img-mapa-polinizacion")[0].offsetHeight;
        var espacioStart = (window.innerHeight - alturaImagen) / 2;
        return `top +=${espacioStart}px`
    }

    return (
        <div id="perdida-de-procesos-ecologicos">
            <div className="container-fluid row mx-0 justify-content-end align-items-start pt-5 px-0">
                <div className="col-lg-6 pl-5 d-flex justify-content-lg-end">
                    <div
                        className="introduccion-textos introduccion-textos-perdidad-procesos"
                        ref={refContenido}
                    >
                        <Textos />
                    </div>
                </div>


                <div
                    className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-perdidad-procesos imagenes-perdida-ecosistemas m-0"
                    ref={refImagenes}
                >
                    <CambioImagenScroll
                        claseMapa="img-mapa-polinizacion"
                        numMapas={2}
                        nombreImagen="impacto_polinizacion"
                        carpeta="perdida_procesos_ecologicos"
                        idioma={language}
                    />
                    <CambioImagenScroll
                        claseMapa="img-mapa-semillas"
                        numMapas={2}
                        nombreImagen="impacto_semillas"
                        carpeta="perdida_procesos_ecologicos"
                        idioma={language}
                    />
                    <div className="d-none">
                        <CambioImagenScroll
                            claseMapa="img-mapa-semillas-none"
                            numMapas={2}
                            nombreImagen="impacto_semillas"
                            carpeta="perdida_procesos_ecologicos"
                            idioma={language}
                        />
                    </div>
                    <div className="mt-5">
                        <img src={`./img/perdida_procesos_ecologicos/severidad_${language}.jpg`} alt="imagen de severidad" className="img-fluid" />
                    </div>
                    <DownloadButton
                        linkDescarga = "https://drive.google.com/drive/folders/1R4X6c4LNuEnTx8uZI7QmoUrR5vPVHcgv?usp=sharing"
                    />
                </div>
            </div>
        </div>
    )
}

export default PerdidaProcesosEcologicos;