import React, { useRef } from 'react';
import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
//Componentes
import FadeElement from '../../components/FadeElement';
import EspacioInferior from '../../components/EspacioInferior';
import useIsMobile from '../../hooks/useIsMobile';

const Conclusiones = () => {

  const Textos = () => {
    const state = useTrackedState();
    const { language } = state;
    return (
      <>
        {language === 'es' &&
          <>

            <p className="mt-lg-3">Colombia es un país privilegiado en términos de su riqueza ecológica y biológica. Sin embargo, el estado actual de sus ecosistemas, expresado en la evaluación de la LRE, indica que estos no han sido reconocidos de forma apropiada por nuestra sociedad y que no se les ha dado la importancia que ameritan para el desarrollo de nuestro país. Si queremos mantener y recuperar esta riqueza, debemos promover mejores mecanismos de conocimiento y de gestión, que nos aseguren un adecuado manejo de ellos.</p>

            <p>Aunque en la actualidad aún subsiste cerca de un 66 % del área del país en la que sus ecosistemas se encuentran en buen estado de conservación, esto no es consecuencia de una buena gestión. Tiene más que ver con la gran extensión de nuestro país con muchas áreas remotas, donde la inaccesibilidad asegura bajos niveles de presión humana y permite que estos se conserven en buen estado y mantengan sus valores.</p>

            <p>Sin embargo, el 34 % del territorio restante que ha sido alterado por las actividades humanas muestra dos aspectos que deben preocuparnos: primero, que esto ha determinado un alto nivel de pérdida y degradación de ecosistemas que ha puesto en riesgo más de un tercio de los tipos ecológicos que conforman esta riqueza; en segundo lugar, el hecho de que se mantengan altos niveles de amenazas sobre muchos de los ecosistemas remanentes, determinados por usos inapropiados y con frecuencia con muy bajos niveles de productividad.</p>

            <p>En resumen, el costo ambiental de los usos actuales de la tierra en el país es desproporcionado frente a los beneficios que genera a la sociedad. La evaluación que arroja la LRE ratifica que el proceso de deterioro ambiental es real, e identifica áreas geográficas críticas a este respecto.</p>

            <p>La evaluación de la LRE nos permitió analizar qué tanto el Sistema de Áreas Protegidas está garantizando la conservación de los ecosistemas remanentes en sus diferentes categorías de riesgo. También nos permitió conocer las áreas geográficas donde desaparecieron los ecosistemas que están catalogados como en alto riesgo, y, dentro de estas, aquellas que podrían ser de interés prioritario para la restauración, si optamos por restablecerlos.</p>

            <p>De esta manera podemos ver que la LRE tiene muchas posibles aplicaciones prácticas para el manejo del territorio y sus recursos, al proporcionar información para orientar la política y la gestión ambiental del territorio. Esta es una primera identificación de los tipos de ecosistemas terrestres colombianos más afectados, su ubicación, y de los principales factores y procesos amenazantes actuales. Pero debe tenerse en cuenta que las interacciones de las sociedades con los ecosistemas son dinámicas, de manera que, para que un instrumento como la LRE tenga toda su utilidad práctica, se requiere que exista un mecanismo de actualización a intervalos regulares no superiores a cinco años.</p>

            <p>La LRE también puede convertirse en un apoyo importante para la construcción y evaluación de escenarios futuros alternativos de uso de la tierra y gestión territorial que posibiliten orientar el desarrollo del país en términos de objetivos sostenibles a mediano y largo plazo, y que permitan hacer un seguimiento disciplinado a los compromisos relacionados con las metas globales de los acuerdos internacionales. Por último, este instrumento puede ser útil como herramienta para mejorar el diálogo entre los generadores de información, los tomadores de decisiones y los actores en los territorios.</p>

          </>
        }
        {language === 'en' &&
          <>

            <p className="mt-lg-3">Colombia is a privileged country in terms of its ecological and biological wealth. However, the current state of its ecosystems, expressed in the evaluation of the RLE, indicates that this has not been properly recognized by the Colombian society, and has not been given the importance they deserve for the development of our country. If we want to maintain and recover this wealth, we must promote better knowledge and management mechanisms, which will ensure their conservation.</p>

            <p>Although there still is about 66 % of the country's area where its ecosystems are in a good state of conservation, this is not a consequence of good management. It has more to do with the great extent of the country with many remote areas, where inaccessibility ensures low levels of human pressure and allows ecosystems to remain in good condition and maintain their values.</p>

            <p>However, the 34 % of the territory that has been altered by human activities shows two aspects that should concern us: First, it has determined a high level of loss and degradation of ecosystems putting more than a third of the ecological types at risk; secondly, high levels of threat remain on many of the remnant ecosystems, often determined by inappropriate land uses and often with very low levels of productivity.</p>

            <p>In summary, the environmental cost of current land uses in the country is disproportionate to the benefits it is generating for society. The evaluation of the RLE confirms that the process of environmental deterioration is real, and identifies critical geographic areas in this regard.</p>

            <p>The evaluation of the RLE allowed us to analyze how far the current Protected Areas System ensures the conservation of the remnant ecosystems in their different risk categories. It also allowed to identify the geographical areas where the ecosystems that are classified as high risk have disappeared, and within these, those that could be of priority for restoration if we choose to restore them.</p>

            <p>In this way it is shown that the RLE has many possible practical applications for the management of the land and its resources, by providing information to guide the environmental policy and management. This is a first identification of the most affected types of Colombian terrestrial ecosystems, their location, and the main current threatening factors and processes. But it must be kept in mind that the interactions of societies with ecosystems are dynamic, therefore, for an instrument like the RLE to have all its practical utility, it requires a mechanism to update it at regular intervals not exceeding 5 years.</p>

            <p>The RLE can also become an important support for building and evaluating alternative future scenarios of land use and territorial management, to ensure the development of the country in terms of sustainable objectives in the medium and long term is met, by encouraging a disciplined monitoring of commitments related to the global goals of international agreements. Finally, this instrument can be a useful tool to improve dialogue between information generators, decision makers and actors in the territories.</p>

          </>

        }
      </>
    )
  }


  const state = useTrackedState();
  const { language } = state;
  const img = useRef();
  const isMobile = useIsMobile();
  const [heightContainer, containerRef, headerSpace] = useHeightContainer();



  return (
    <div>
      <div className="container-fluid row mx-0  flex-lg-nowrap  justify-content-end py-3 align-items-start px-0">
        <div className="col-lg-6 pl-5 d-flex justify-content-center px-5">
          <div className="introduccion-textos conclusiones">
            <div className="title-sticky"
              style={{
                top: `${headerSpace}px`
              }}
            >
              {language === 'es' &&
                <>
                  <h2>Conclusiones</h2>
                </>
              }
              {language === 'en' &&
                <>
                  <h2>Conclusions</h2>
                </>
              }

            </div>
            <Textos />
          </div>
        </div>

        <div
          className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-conclusiones m-0 my-5 my-lg-0"
          style={{
            position: "sticky",
            top: heightContainer / 2
          }}
        >
          <div
            ref={containerRef}
          >
            <img ref={img} src={`./img/introduccion/ambiente2.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
          </div>
        </div>
      </div>
      <FadeElement />
      <EspacioInferior />
    </div>
  )
}

export default Conclusiones;