import React from 'react';
import Sections from '../Store/Sections.js'
import { NavLink, useHistory } from 'react-router-dom';
import { useTrackedState } from '../Store';
import normalizeString from '../js/utilities/normalizeString.js';


const Menu = ({ open, handleClick }) => {
  const state = useTrackedState();
  const { language } = state;

  return (
    <div className="row mx-0 main-menu flex-lg-nowrap justify-lg-content-between" id="menu" open={open}
      style={{
        background: '#fff url("./img/franja_menu.png") center/cover no-repeat',
        left: `${open ? '0' : '-100%'}`
      }}
    >
      {Sections.map((item, index) =>
        <div className="w-100 px-2 col-lg-auto" key={`itemMenu${index}`}>
          <NavLink
            className="text-center d-flex flex-column justify-content-center px-2"
            to={`${normalizeString(item[language])}`}
            activeClassName="menu-selected"
            onClick={() => { handleClick(false); }}
          >
            <p className="mb-0">{item[language]}</p>
          </NavLink>
        </div>
      )}
    </div>
  )
}

export default Menu;