import React from 'react';
import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';

const EstadoActualEcosistemas = () => {
    const [headerSpace] = useHeightContainer();
    const state = useTrackedState();
    const { language } = state;
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                {language === 'es' &&
                    <>
                        <p className="mt-lg-3">Estas imágenes muestran el estado actual de transformación de los ecosistemas más críticos del país. Se observan diferentes tipos de uso (ganadería, agricultura campesina, agricultura industrial) y los procesos de fragmentación y erosión asociados.</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <p className="mt-lg-3">These images show the current transformation state of the most critical ecosystems in the country. Different types of use are observed (livestock, peasant agriculture, industrial agriculture) and the associated fragmentation and erosion processes.</p>
                    </>
                }
            </>
        )
    }
    const ItemUno = () => {
        return (
            <>
                {language === 'es' &&
                    <>
                        <p>Bosques secos tropicales del Caribe</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <p>Tropical dry forests of the Caribbean</p>
                    </>
                }
            </>
        )
    }

    const ItemDos = () => {
        return (
            <>
                {language === 'es' &&
                    <>
                        <p>Arbustales secos altoandinos de la cordillera Oriental</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <p>Dry shrublands of the Eastern Andean Highlands</p>
                    </>
                }
            </>
        )
    }

    const ItemTres = () => {
        return (
            <>
                {language === 'es' &&
                    <>
                        <p>Arbustales y cardonales de valles intraandinos</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <p>Shrub-and-teasel lands of the Andean internal valleys</p>
                    </>
                }
            </>
        )
    }

    const ItemCuatro = () => {
        return (
            <>
                {language === 'es' &&
                    <>
                        <p>Bosques subhúmedos del altiplano cundiboyacense</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <p>Sub-humid forests of the Cundinamarca-Boyacá high plateau</p>
                    </>
                }
            </>
        )
    }

    const ItemCinco = () => {
        return (
            <>
                {language === 'es' &&
                    <>
                        <p>Humedales del altiplano de la Cordillera Oriental</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <p>High-plateau wetlands in the Eastern Mountains</p>
                    </>
                }
            </>
        )
    }

    return (
        <div className="row mx-auto py-lg-5 align-items-start pl-lg-5 mb-5 estado-actual">
            <div className="row mx-auto py-5 align-items-start">
                <div className="col-lg-4 pl-5 pt-lg-5">

                    <div 
                        className="title-sticky"
                        style={{
                            top: `${headerSpace+40}px`
                        }}
                    >
                        {language === 'es' &&
                            <>
                                <h2>Estado actual de ecosistemas en riesgo</h2>
                            </>
                        }
                        {language === 'en' &&
                            <>
                               <h2>Current Condition of Ecosystems under High Risk</h2>
                            </>
                        }

                    </div>
                    <Textos />
                </div>
                <div className="col-12 mx-auto mx-lg-0 col-lg-10"
                    /* style={{
                        position: 'sticky',
                        top: 0
                    }} */
                >
                    <div
                        className="contenedor-imagenes w-100"
                    >
                        <div className="item-B10 item-collage d-flex justify-content-center  align-items-center"
                            style={{
                                backgroundImage: "url(./img/estado_actual/estado_actual/b10.jpg)",
                            }}
                            alt="B10"
                        >
                            <p className="item-numero ">1</p>

                            <div className="item-text ">
                                <h2>B10</h2>
                                <ItemUno />
                            </div>
                        </div>

                        <div className="item-A6 item-collage d-flex justify-content-center  align-items-center"
                            style={{
                                backgroundImage: "url(./img/estado_actual/estado_actual/a6.jpg)",
                            }}
                            alt="A6"
                        >
                            <p className="item-numero">2</p>

                            <div className="item-text ">
                                <h2>A6</h2>
                                <ItemDos />
                            </div>
                        </div>

                        <div className="item-A5 item-collage d-flex justify-content-center  align-items-center"
                            style={{
                                backgroundImage: "url(./img/estado_actual/estado_actual/a5.jpg)",
                            }}
                            alt="A5"
                        >
                            <p className="item-numero">3</p>
                            <div className="item-text ">
                                <h2>A5</h2>
                                <ItemTres />
                            </div>
                        </div>

                        <div className="item-B22 item-collage d-flex justify-content-center  align-items-center"
                            style={{
                                backgroundImage: "url(./img/estado_actual/estado_actual/b22.jpg)",
                            }}
                            alt="B22"
                        >
                            <p className="item-numero">4</p>
                            <div className="item-text ">
                                <h2>B22</h2>
                                <ItemCuatro />
                            </div>
                        </div>

                        <div className="item-P1 item-collage d-flex justify-content-center  align-items-center"
                            style={{
                                backgroundImage: "url(./img/estado_actual/estado_actual/p1.jpg)",
                            }}
                            alt="P1"
                        >
                            <p className="item-numero">5</p>
                            <div className="item-text ">
                                <h2>P1</h2>
                                <ItemCinco />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EstadoActualEcosistemas;