import React, {useReducer} from 'react';
import { createContainer } from 'react-tracked';
import {Link} from 'react-router-dom';
const BotonHeader = () => {
  return(  
    <Link 
      to="/"
      className="icon-home mr-3"
      style={{
        width: `40px`,
        height: `40px`,
        background: `url(${'./img/home.svg'}) center / cover no-repeat`
      }}
    />
  )
}
const initialState = {
  language: "es",
  activePage: 1,
  query: 'my query',
  title: {
    es: () => <div className="d-flex align-items-center col-12 col-lg-13 text-right text-lg-left px-0"><BotonHeader/><h2>Ecosistemas colombianos | <span>Amenazas y riesgos</span></h2></div>,
    en: () => <div className="d-flex align-items-center col-12 col-lg-13 text-right text-lg-left px-0"><BotonHeader/><h2>Colombian Ecosystems | <span>Threats and Risks</span></h2></div>
  },
  languages: ['en', 'es']
};
const reducer = (state, action) => {
  switch (action.type) {
    case 'SET_GENERAL_STATE':
      return{
        ...state,
        [action.key] : action.value
      }
    default:
      return state;
  }
};

const useValue = () => useReducer(reducer, initialState);

export const {
  Provider,
  useTrackedState,
  useUpdate: useDispatch,
} = createContainer(useValue);