import React, { useEffect } from 'react';
import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import ReactTooltip from 'react-tooltip';
import MapaContainer from '../../components/MapaContainer';


const TipoEcosistemas = () => {
  const [heightContainer, containerRef, headerSpace] = useHeightContainer();

  const Textos = () => {
    const state = useTrackedState();
    const { language } = state;
    useEffect(() => {
      ReactTooltip.rebuild();
    }, [language]);
    return (
      <>
        {language === 'es' &&
          <>

            <div className="mt-lg-3">
              <p>Uno de los aspectos ambientales sobresalientes de Colombia es su alta variabilidad ambiental en relación con su extensión geográfica, lo cual ha generado una gran diversidad de ecosistemas <span
                className="nota-al-pies-number" data-tip="<span>[12]</span>. Etter, A. (1998). Diversidad ecosistémica: 1. Introducción general. En M. E. Chaves y N. Arango (Eds.), <em>Informe nacional sobre el estado de la biodiversidad: Colombia</em> 1997 (pp. 4–10). Bogotá: Instituto Alexander von Humboldt, PNUMA y Ministerio del Medio Ambiente." data-for="tooltip-tipo-ecosistemas">[12]</span>, que a su vez son la explicación de su megadiversidad de especies (10 % de la biodiversidad mundial), en particular con altos niveles de endemismo <span
                  className="nota-al-pies-number" data-tip="<span>[13]</span>. Hernández, J., Ortiz, R., Walschburger, T. y Hurtado, A. (1992). <em>Estado de la biodiversidad en Colombia. La diversidad biológica de Iberoamérica.</em> México: I. Cyted.
            " data-for="tooltip-tipo-ecosistemas">[13]</span>. La variabilidad ambiental resulta de la confluencia geográfica con una orografía con grandes variaciones en altitud (0-5800 m s. n. m.), amplios rangos de precipitación media anual (200-10 000 mm) y de número de meses secos (0-10); de los contrastes en las condiciones del sustrato geológico y de los suelos, y de la dinámica del agua superficial y subterránea. Así, podemos encontrar ecosistemas pertenecientes a biomas que van desde desiertos y sabanas tropicales hasta bosques tropicales hiperhúmedos y montañas tropicales cubiertas de nieve, pasando por diferentes estados intermedios.</p>

              <p>La clasificación empleada en el mapa de ecosistemas de Colombia se fundamenta en el modelo conceptual de H. Walter  <span
                className="nota-al-pies-number" data-tip="<span>[14]</span>. Walter, H. (1979). <em>Vegetation of the Earth and Ecological Systems of the Geo-Biosphere</em> (2.ª ed.). Nueva York: Springer." data-for="tooltip-tipo-ecosistemas">[14]</span>. Este sistema propone un modelo de clasificación de tipo funcional, que agrupa los biomas y ecosistemas de la Tierra utilizando conceptos de zonalidad y azonalidad resultantes de la interacción entre el clima, la topografía y los suelos. Para ello involucra gradientes de balance hídrico (de muy húmedo a seco) moldeados por gradientes de altitud/temperatura y gradientes/particularidades por cambios en la presencia de nutrientes y agua en el sustrato. Este sistema permitió reconocer a una escala general más de 80 ecosistemas en el país. Dentro de estos pueden aún identificarse variantes biogeográficas que todavía han de ser refinadas con base en los modelos de distribución de especies.</p>
            </div>
          </>
        }
        {
          language === 'en' &&
          <>

            <div className="mt-lg-3">
              <p>One of the outstanding environmental aspects of Colombia is its high environmental variability in relation to its geographical extent, which has generated a great diversity of ecosystems <span
                className="nota-al-pies-number" data-tip="<span>[12]</span>. Etter, A. (1998). Diversidad ecosistémica: 1. Introducción general. In M. E. Chaves and N. Arango (Eds.), <em>Informe nacional sobre el estado de la biodiversidad: Colombia 1997</em> (pp. 4–10). Bogotá: Instituto Alexander von Humboldt, PNUMA, and Ministerio del Medio Ambiente." data-for="tooltip-tipo-ecosistemas">[12]</span>, which in turn explain its mega-diversity in terms of species (10 % of the world biodiversity), and in particular their high levels of endemism <span
                  className="nota-al-pies-number" data-tip="<span>[13]</span>. Hernández, J., Ortiz, R., Walschburger, T., and Hurtado, A. (1992). <em>Estado de la biodiversidad en Colombia. La diversidad biológica de Iberoamérica.</em> México: I. Cyted." data-for="tooltip-tipo-ecosistemas">[13]</span>. This environmental variability results from the geographical confluence of an orography with large altitude variations (0-5800 m), wide annual average precipitation ranges (200-10 000 mm) and number of dry months (0-10); the contrasting conditions of the geological substrates and of the soils, and of the dynamics of surface and underground water. This leads to the existence of ecosystems belonging to biomes that range, from deserts and tropical savannas, to hyper-humid tropical forests and snow-capped tropical mountains, and different intermediate states.
            </p>

              <p>The classification used in the Ecosystem Map of Colombia is based on the conceptual model of H. Walter <span
                className="nota-al-pies-number" data-tip="<span>[14]</span>. Walter, H. (1979). <em>Vegetation of the Earth and Ecological Systems of the Geo-Biosphere</em> (2nd ed.). New York: Springer." data-for="tooltip-tipo-ecosistemas">[14]</span>. This system proposes a functional classification model, which groups the terrestrial biomes and ecosystems of the Earth's land using concepts of climatic zoning and azonal/intrazonal differences resulting from the interaction between climate, topography and soils. For this, it uses water balance gradients (very wet to dry) molded by altitude/temperature gradients and specificities arising from nutrient availability and water dynamics in the substrate. This system allowed to recognize over 80 ecosystems for the country on the general scale applied. Within these, biogeographic variants of these ecosystems can still be identified, which will need to be refined based on biogeographic and species distribution models.</p>
            </div>
          </>
        }

        <ReactTooltip
          id="tooltip-tipo-ecosistemas"
          place="top"
          html={true}
          clickable={true}
          effect={'solid'}
          type={'info'}
          event={'click'}
          globalEventOff={'click'}
          className={'nota-al-pie'}
        >
        </ReactTooltip>

      </>
    )

  }

  const state = useTrackedState();
  const { language } = state;
  useEffect(() => {
  }, [language]);
  return (
    <div className="container-fluid row mx-0 py-5 align-items-start position-relative">
      <div className="col-lg-6 pl-5 d-flex justify-content-center px-5">
        <div className="introduccion-textos">
          <div className="title-sticky"
            style={{
              top: `${headerSpace}px`
            }}
          >
            {language === 'es' &&
              <>
                <h2>Tipos de ecosistemas</h2>
              </>
            }
            {language === 'en' &&
              <>
                <h2>Types of Ecosystems</h2>
              </>
            }

          </div>
          <Textos />
        </div>
      </div>
      <div className="col-lg-8"
        style={{
          position: 'sticky',
          top: 0
        }}
      >

        <div
          ref={containerRef}
          style={{ height: `${heightContainer}px` }}
          className="d-lg-flex flex-column align-items-center justify-content-center  height-responsive"
        >
          <MapaContainer
            img={`./img/introduccion/tipos_ecosistemas_${language}.jpg`} className="container-imagen-completa"
          />
          
        </div>

      </div>
    </div>
  )
}

export default TipoEcosistemas;